<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">
                
                        <div class="modal-dialog ui-draggable" style="display: inline-block; max-width: 500px;">
                            <div class="modal-content">
                                <div class="modal-header ui-draggable-handle">
                                    <h5 id="ui-id-26" class="modal-title">{{$t('AddDuplicateEntry')}}</h5><button type="button" title="" @click="$emit('close')" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                                </div>
                                <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                                    <section id="main-content" class="main-content">
                                        <section class="main-content">
                                            <div class="container-fluid p-0">
                                                <div class="col-md-12 p-0">
                                                   
                                                    <ValidationObserver  v-slot="{ handleSubmit }">
                                                        
                                                        <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                                                             
                                                            <div class="row">

                                                                <div class="col-md-12">
                                                                    <div class="form-group">
                                                                        <label>{{$t('ScheduleDate')}}<span class="text-danger">*</span></label> 
                                                                        <input type="date" v-model="date" disabled class="form-control"/> 
                                                                    </div>
                                                                </div>



                                                                <div class="col-lg-6">
                                                                    <div class="form-group">
                                                                        <label>{{$t('StartDateTime')}}</label>
                                                                        <div class="input-group mb-3">
                                                                        <vue-timepicker class="w-100" v-model="startTime" format="HH:mm" hide-clear-button></vue-timepicker>                                                                        
                                                                    </div>
                                                                   </div>
                                                                </div>

                                                                <div class="col-lg-6">
                                                                    <div class="form-group">
                                                                        <label>{{$t('EndDateTime')}}</label>
                                                                        <div class="input-group mb-3">
                                                                        <vue-timepicker class="w-100" v-model="endTime" format="HH:mm" hide-clear-button></vue-timepicker> 
                                                                    </div>
                                                                   </div>
                                                                </div>
                                                                
                                                               
                                                            </div>
                                                            
                                                            <div class="row flex-row-reverse mt-4">
                                                                
                                                                    <div class="text-end text-right col-12">
                                                                        <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{$t('Save')}}</button>
                                                                        <button @click="$emit('close')" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{$t('Cancel')}}</button>
                                                                     </div>
                                                               
                                                            </div>
    
                                                        </form>
                                                    </ValidationObserver>
                                                </div>
                                            </div>
                                        </section>
                                    </section>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message" v-bind:open="store.getAlertMessage.open"  v-on:close="store.$state.getMessageAlert.open=false"/>
    </div>
    </template>
    
    <style>
    .singleselectlook .multiselect__select::before{z-index:99;}
    .singleselectlook .multiselect__tags{padding: 8px 30px 0 8px !important;min-height: 36px !important;}
    .singleselectlook .multiselect__input{margin-bottom: 0px;}
    </style>
    
    <script>
    import { defineComponent,} from '@vue/composition-api'
    import {  extend, ValidationObserver} from 'vee-validate';
    import { userTimerInfo} from "../../../stores/TimeTrack"
    import Alert from '../../Common/Alert.vue';
    import VueTimepicker from 'vue2-timepicker'
    import {
    getSecondsFromDate,
    formatter
} from "@/assets/common/vue-common.js"
import moment from "moment"
    extend('required', {
        validate(value) {
            return {
                required: true,
                valid: ['', null, undefined].indexOf(value) === -1
            };
            
        },
        message: 'The {_field_} must be required'
        
        ///computesRequired: true
    });
    export default defineComponent({
        name: "Duplicate Entry",
        components: {
            ValidationObserver,
            Alert,
            VueTimepicker
        },
        props:{
            manualData:{
                type:Object
            }
        },
        data() {
            return {
                startTime: '00:00',
                endTime: '00:00',
                date:moment(this.manualData.dateStart).format("YYYY-MM-DD")
            }     
    
        },
        created: function () {
         console.log("manuall",this.manualData);
        },
        methods: {

           async submit(){
            console.log("test",this.manualData)
            if (this.validHours()) {
                this.manualData.createdBy=this.store.getUserInformation.id;
                this.manualData.linkedTrackId=this.manualData.trackLinkedId;
                this.manualData.billableMark=="$" ? this.manualData.isBillable = true : this.manualData.isBillable= false
                this.manualData.type = this.manualData.trackType
                await this.store.addManualyTimer(this.manualData);
                this.$emit('close')
                const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                this.store.getTimerListing1(url)
            }
            },
            validHours() {
            let status = false
            if (!this.startTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": "Please Enter start Date"
                })
            } else if (!this.endTime) {
                this.status = false
                this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": "Please Enter End Date"
                })
            }
            //console.log("end",moment(moment(this.endTime.HH+':'+this.endTime.mm, 'hh:mm')))
            else if (this.startTime && this.endTime) {

                let dateStart = moment(new Date(this.manualData.dateStart), "YYYY-MM-DD").format("YYYY-MM-DD")
                let startDateTime = moment(dateStart + ' ' + this.startTime, "YYYY-MM-DD HH:mm")
                let endDateTime = moment(dateStart + ' ' + this.endTime, "YYYY-MM-DD HH:mm")
                let today = moment(moment().format("YYYY-MM-DD HH:mm"))
                if (startDateTime.valueOf() > endDateTime.valueOf() || endDateTime.valueOf() > today.valueOf()) {
                    this.store.alertBOxForMessage({
                        "type": 'warning',
                        "open": true,
                        "message": "You can not enter time for the future. Please update the time entered"
                    })
                } else {
                    status = true
                    let sec = getSecondsFromDate({
                        startDate: moment(this.startTime, 'hh:mm'),
                        endDate: moment(this.endTime, 'hh:mm')
                    }, 'seconds')
                    
                    this.manualData.startDate = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.startTime = startDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.stopTime = endDateTime.format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.endDate = endDateTime .format("YYYY-MM-DD HH:mm:ss")
                    this.manualData.totalHours = formatter(sec)
                }
            }
            return status
        },
    
        },
        setup() {
            const store = userTimerInfo();
            return {
                store,
    
            }
        },
        });
    </script>