<template>
    <section>
        
        <div class="col-md-12 p-0 mt-4 float-left">
        <div class="theme-primary partition p-actions-expand">
            <!-- <a href="javascript:;" value="list" id="pinUnpinId" class="p-action-btn text-white unpinscreen pinUnpinId" pinned="0" title="Pin" data-toggle-tooltip="tooltip" data-original-title="Pin"> <i class="fa fa-thumb-tack "></i><br></a> -->
            <span class="p-name text-white"> {{ $t('ActivitiesDoneByYou') }}</span>
            <span class="p-actions float-right newaction_control">
                <a id="aDateLable_" href="javascript:;" class="p-action-btn text-white showDatePanel pt-8rem bgdark_blkbar border-0 font-14" @click="toggleDatePanel"><i class="fa fa-bars mt-1 pr-2"></i><em class="text-white ">{{ selectedName }}</em></a>
                <!-- <a @click="selectMonth" value="cal" id="ShowCalender" class="p-action-btn text-white" title="" data-toggle-tooltip="tooltip" data-original-title="Calendar View"><i class="fa fa-calendar"></i><br><span>This Month</span></a> -->
                <a href="javascript:;" @click="graphView(true)"  class="p-action-btn text-white ddlAction" ><i aria-hidden="true" :class="listButtonShow ? 'fa fa-list':'fa fa-pie-chart'"></i><br>{{ listButtonShow ? $t('ListView') :$t('GraphView') }}</a>
                <a href="javascript:;" @click="openmodal(true)" forvalue="Non-Billable" title="" class="p-action-btn text-white ddlAction" data-toggle-tooltip="tooltip" data-original-title="  Non-Billable"><i aria-hidden="true" class="fa fa-pencil" ></i><br> {{ $t('Edit') }} </a>
                <a href="javascript:;" forvalue="Delete" title="" id="DeleteItem" class="p-action-btn text-white iconenable ddlAction" data-toggle-tooltip="tooltip" @click="UpdateOrDeleteEntries()"><i aria-hidden="true" class="fa fa-trash"></i><br>{{ $t('DeleteEntries') }} </a>
            </span> <span class="p-actions float-right">
                <!---->
            </span><div></div>
        </div>
        <div v-if="isDatePanelVisible" id="DivTimer_" data-pageaction="" class="DatePopUp divtimer_design p-1 R75" style="display: block;">
             <table class="table-responsive AssetDatePopUp d-block ">
                 <tbody class="daterangebody">
                     <tr class="daterangetr">
                         <td class="sep daterangetd">
                             <ul class="date_select">
                                 <li><a @click="getListByDateType('Today',$t('Today'))" title="" id="aToday" class="Today" data-toggle-tooltip="tooltip" data-original-title="Today">{{ $t('Today') }}</a></li>
                                 <li><a @click="getListByDateType('Yesterday',$t('Yesterday'))" title="" id="aYesterday" class="Yesterday" data-toggle-tooltip="tooltip" data-original-title="Yesterday">{{ $t('Yesterday') }}</a></li>
                                 <li><a @click="getListByDateType('thisweek',$t('thisWeek'))" title="" id="aWeek" class="Week" data-toggle-tooltip="tooltip" data-original-title="This Week">{{ $t('thisWeek') }}</a></li>
                                 <li><a @click="getListByDateType('thisyear',$t('thisYear'))" title="" id="aAll" class="AllTime" data-toggle-tooltip="tooltip" data-original-title="All Time">{{ $t('thisYear') }}</a></li>
                             </ul>
                         </td>
                         <td valign="top" class="daterangetd">
                             <ul class="date_select">
                                 <li><a  @click="getListByDateType('LastWeek',$t('LastWeek'))" id="aLastWeek" class="LastWeek" title="" data-toggle-tooltip="tooltip" data-original-title="Last Week">{{ $t('LastWeek') }}</a></li>
                                 <li><a  @click="getListByDateType('thismonth',$t('thisMonth'))"  id="aMonth" class="Month" title="" data-toggle-tooltip="tooltip" data-original-title="This Month">{{ $t('thisMonth') }}</a></li>
                                 <li><a  @click="getListByDateType('LastMonth',$t('LastMonth'))" id="aLastMonth" class="LastMonth" title="" data-toggle-tooltip="tooltip" data-original-title="Last Month">{{ $t('LastMonth') }}</a></li>
                             </ul>
                         </td>
                     </tr>
                     <tr class="float-left p-0 m-0 mt-1 col-12 daterangetr">
                         <td class="col-4 m-1 daterangetd  float-left table_datepickerfix">
                             <label>From ...</label><br>
                             <input id="txtFromDate_" name="startDate" type="date" class="textfield2 ie_crossicon_none FromDate form-control p-1 hasdatetimepicker notvue" autocomplete="off" data-target="#txtFromDate_" data-toggle="datetimepicker" title="Select Date" placeholder="M/D/YYYY" v-model="From">
                         </td>
                         <td class="col-4 m-1 daterangetd  float-left table_datepickerfix">
                             <label>...To</label><br>
                             <input id="txtToDate_" name="endDate" type="date" class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue" autocomplete="off" data-target="#txtToDate_" data-toggle="datetimepicker" title="Select Date" placeholder="M/D/YYYY" v-model="To">
                         </td>
                         <td class="col-2 m-1 ml-2 daterangetd float-left table_datepickerfix">
                             <input type="button" id="btnsearch_"  class="btn btn-success float-left p-1 p-1 pl-4 pr-4" data-result="btnsearch" value="Go" @click="getListByDateType('thisweek','This Week')">
                         </td>
                     </tr>
                 </tbody>
             </table>
         </div>


<div class="col-md-12 px-2 py-1 float-left bg-f5f5f5">
<div class="row">
         <div class="col-sm-12 col-md-7 col-lg-7 col-xl-7 mb-2 pt-2 responsivefix991 lft">
                <div class="mr-2 d-inline-block">
                    <span class="legends-purple colorboxsquare"></span>{{ $t('Automatic') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_low colorboxsquare"></span> {{ $t('Manual') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_medium colorboxsquare"></span>{{ $t('Timesheet') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_profile colorboxsquare"></span>{{ $t('Leave') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="legends_high colorboxsquare"></span> {{ $t('Holiday') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="lg-pausedresumed colorboxsquare"></span> {{ $t('PausedResumed') }}
                </div>
            </div>

            <div class="col-sm-12 col-md-5 col-lg-5 col-xl-5 responsivefix991 rgt">
                  <div class="w-70 float-right p-0">
                   <div class="w-100 mt-0">
                        <div class="float-right p-0">
                            <div class="background-0 text-left form-check-inline mr-0">
                                <a id="anctimersearch" href="javascript:;" class="btn btn-success mr-1 px-4"  data-original-title="Search" @click="getListByDateType('All',$t('ALL'))"><i class="fa fa-search" ></i></a>
                                <a href="javascript:;" title="" id="anccleartimersearch" class="btn btn-danger texttimerrest px-4" data-toggle-tooltip="tooltip" data-original-title="Clear all " @click="clearAllFilters('All',$t('ALL'))"><i class="fa fa-refresh" ></i></a>
                            </div>
                        </div>
                        <div class="col-7 float-right pl-0">
                            <div class="form-group mb-0">
                                <input id="texttimersearch" maxlength="50" class="form-control" type="text"  placeholder="Search By Project/Task/Description" autocomplete="off" v-model="search">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            </div>

    </div></div>


    <div class="col-md-12 p-0 mt-4 float-left border" >
        <div class="row tt_status">

            <div class="col tt_bod-right">
                <em class="tt_icons ico_totalhrs float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">Total</span>
                    <span class="w-100 text-large">{{getTimerListing.trackHours.totalHours}}</span>
                </span>
            </div>

            <div class="col tt_bod-right">
                <em class="tt_icons ico_totalpaidtime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">Total Paid Time</span>
                    <span class="w-100 text-large">{{ getTimerListing.trackHours.paidHours }}</span>
                </span>
            </div>

            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak=='yes'">
                <em class="tt_icons ico_totalbreaktime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">Total Break Time</span>
                    <span class="w-100 text-large">{{getTimerListing.trackHours.totalBreakHours }}</span>
                </span>
            </div>

            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak=='yes'">
                <em class="tt_icons ico_paidbreaktime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">Paid Break Time</span>
                    <span class="w-100 text-large">{{getTimerListing.trackHours.paidBreakHours }}</span>
                </span>
            </div>
            <div class="col tt_bod-right" v-if="store.getTimer.enableBreak!='yes'">
                <em class="tt_icons ico_totalpaidtime float-left"></em>
                <span class="grid">
                    <span class="w-100 text-small mt-3">Paused Time</span>
                    <span class="w-100 text-large">{{getTimerListing.trackHours.pausedHours }}</span>
                </span>
            </div>
        </div>
       
    </div>
    <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message" v-bind:open="store.getAlertMessage.open"  v-on:close="store.$state.getMessageAlert.open=false"/>
     <!-- <div><span class="ml-3">{{ dateType }}</span></div>  -->
    <BulkEdit v-if="bulkEdit" v-on:close="openmodal(false)"/>
    </section>
</template>



<script>
import { defineComponent,onMounted, reactive,ref  } from '@vue/composition-api'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import { storeToRefs } from 'pinia';
import DataService from '../../services/DataService';
import BulkEdit from './modals/BulkEdit.vue';
import Alert from '../Common/Alert.vue';
import i18n from '../../localization/index';
export default defineComponent ({
    components: {
        BulkEdit,
        Alert   
    },
    
    setup() {
        const store = userTimerInfo();
        const {getTimerListing,selectedValues} = storeToRefs(store)
        onMounted(()=>{
            const url = `dateType=${store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
             store.getTimerListing1(url)
            
        })
        return {
            store,
            selectedValues
            ,getTimerListing
            
        }
    },

    data(){
        return{
              dateType:this.store.$state.dateFilterType,
              isDatePanelVisible: false,
              From:'',
              To:'',
              search:'',
              bulkEdit:false,
              selectedName:i18n.t('Today'),
              listButtonShow:false

        }
    },
    methods:{
        graphView(){
            this.listButtonShow = this.listButtonShow = !this.listButtonShow
            this.$emit("graphView",this.listButtonShow)
        },
        selectMonth(){
            this.$emit("selectMonth",this.dateType)
        },
        UpdateOrDeleteEntries(){
            var vm =this;
                if(this.store.$state.selectedValues.length >0 ){
                    var id=this.store.$state.selectedValues
            var params = `ids=${id}&ActionType=Delete`;
            DataService.setAction(params).then((response) => {
                //var data=response
                if(response.data=="DeletedSuccessfully"){
                    vm.ShowAlert(i18n.t('DeletedSuccessfully'), "success", true, i18n.t('Alert'));
                    
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                     vm.store.getTimerListing1(url)
                        }
                    else{
                         vm.ShowAlert(i18n.t('Failure'), "error", true, i18n.t('Alert'));
                        }
              
             });
            }else{


                    this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": "Please  select check box"
                })
                
           
               
           
            }

        },
        toggleDatePanel() {
      this.isDatePanelVisible = !this.isDatePanelVisible;
    },
     getListByDateType(type,name){
            this.dateType = type
            this.store.$state.dateFilterType = type
            this.store.$state.From=this.From
            this.store.$state.To=this.To
            const url = `dateType=${this.dateType}&search=${this.search}&toDate=${this.To}&fromDate=${this.From}&newTrackId=0`
             this.store.getTimerListing1(url)
            var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}`
            this.store.TimerTeamStatus(params)
             this.isDatePanelVisible=false;
             this.selectedName = name
        },
        openmodal(event){
            if(event){
                if(this.store.$state.selectedValues.length >0 ){
                this.bulkEdit = event
                }else{
                    this.store.alertBOxForMessage({
                    "type": 'warning',
                    "open": true,
                    "message": "Please  select check box"
                })
                }
            }else{
                this.bulkEdit = event
            }
            // bulkEdit=!bulkEdit
            
        },
        clearAllFilters(type,name){
            this.search = ''
            this.dateType = type
            this.store.$state.dateFilterType = type
            const url = `dateType=${this.dateType}&search=${this.search}&toDate=${this.To}&fromDate=${this.From}&newTrackId=0`
             this.store.getTimerListing1(url)
             this.selectedName = name
        }
      

    }
})
</script>