<template lang="en">
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable" style="display: inline-block; max-width: 500px;">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle">
                    <h4 name="header" class="modal-title">
                        {{ $t('Edit') }} {{ store.$state.selectedValues.length }} {{ $t('Entry') }}
                    </h4>
                    <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times" @click="$emit('close')"></em> </button>
                </div>

                <ValidationObserver ref="form" v-slot="{ handleSubmit }" class="listing py-3 px-3">
                    <form v-on:submit.prevent="handleSubmit(onSubmit)" ref="form">

                        <div class="row">

                            <div class="col-md-1">
                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" :class="{ 'chkItems custom-control-input': true }" :value="projectEdit" name="DeleteInputs" id="projectEdit" v-model="projectEdit">
                                    <label class="custom-control-label universal-custom-control-label pt-1" for="projectEdit"></label>
                                </div>
                            </div>
                            <div class="col-md-2 p-0"><label class="">{{ $t('ProjectTitle') }}:</label></div>
                            <div class="col-md-9">
                                <ValidationProvider v-slot="{ errors }">
                                    <multiselect :placeholder="$t('searchProject')" @input="taskByProject" v-model="bulkData.projectId" :options="store.getProjectsByUser" label="projectName" track-by="projectId" :show-labels="false">
                                    </multiselect>
                                    <span>{{ errors[0] ? $t('DescRequired') : '' }}</span>
                                </ValidationProvider>
                            </div>

                        </div>

                        <div class="row">

                            <div class="col-md-1">
                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" :class="{ 'chkItems custom-control-input': true }" :value="taskEdit" name="DeleteInputs" id="taskEdit" v-model="taskEdit">
                                    <label class="custom-control-label universal-custom-control-label pt-1" for="taskEdit"></label>
                                </div>
                            </div>
                            <div class="col-md-2 p-0"><label class="">{{ $t('Tasks') }}:</label></div>
                            <div class="col-md-9">
                                <multiselect :placeholder="$t('searchTask')" v-model="bulkData.taskId" :options="store.getTasksFromProject" label="firstAttribute" track-by="value" :show-labels="false">
                                </multiselect>
                            </div>

                        </div>

                        <div class="row">
                            <div class="col-md-1">
                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" :class="{ 'chkItems custom-control-input': true }" :value="tagsEdit" name="DeleteInputs" id="tags" v-model="tagsEdit">
                                    <label class="custom-control-label universal-custom-control-label pt-1" for="tags"></label>
                                </div>
                            </div>
                            <div class="col-md-2 p-0"><label class="">{{ $t('Tags') }}:</label></div>
                            <div class="col-md-9">
                                <multiselect :placeholder="$t('selectTags')" :multiple="true" v-model="tagSelect" :options="store.getTags" label="tagName" track-by="tagId" :show-labels="false">
                                </multiselect>
                            </div>
                        </div>

                        <div class="row">
                            <div class="col-md-1">
                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" :class="{ 'chkItems custom-control-input': true }" :value="paidEdit" name="DeleteInputs" id="paid" v-model="paidEdit">
                                    <label class="custom-control-label universal-custom-control-label pt-1" for="paid"></label>
                                </div>
                            </div>
                            <div class="col-md-2 p-0"><label>{{ $t('Billable') }}:</label></div>
                            <div class="col-md-9">
                                <asp-checkbox-toggle><label class="switch">
                                        <input type="checkbox" id="IsbillableN" v-model="bulkData.isBillable" name="Isbillable" class="dynamic custom-switch"><span class="slider round">
                                            <span class="slider-yes">Yes</span><span class="slider-no">No</span></span></label>
                                </asp-checkbox-toggle>
                            </div>
                        </div>

                        <div class="row">
                            <div class="col-md-1">
                                <!-- <div data-v-094f2467="" class="form-check form-check-inline">
                                    <div data-v-094f2467="" class="custom-control custom-checkbox">
                                        <input data-v-094f2467="" type="checkbox" name="DeleteInputs" id="chk_1024587" class="chkItems custom-control-input" value="1024587" style="display: none;">
                                        <label data-v-094f2467="" for="chk_1024587" class="custom-control-label universal-custom-control-label pt-1"></label></div></div> -->

                                <div class="custom-control custom-checkbox">
                                    <input type="checkbox" :class="{ 'chkItems custom-control-input': true }" :value="descriptionEdit" name="DeleteInputs" id="description" v-model="descriptionEdit">
                                    <label class="custom-control-label universal-custom-control-label pt-1" for="description"></label>
                                </div>

                                <!-- <input type="checkbox" class=" custom-checkbox" v-model="descriptionEdit"/>  -->
                            </div>
                            <div class="col-md-2 p-0"><label class="">{{ $t('Description') }}:</label></div>
                            <div class="col-md-9">
                                <ValidationProvider v-slot="{ errors }">
                                    <textarea id="Taskdescription" name="Description" v-model="bulkData.trackDescription" style="border-color: #BDBCBD; resize:none" rows="10" cols="2000" class="timertxtarea form-control" :class="errors[0] ? 'is-invalid' : ''" :placeholder="$t('PleaseEnterDescription')" maxlength="2000"></textarea>
                                    <span class="text-danger font-weight-bold">{{ errors[0] }}</span>
                                </ValidationProvider>
                            </div>
                        </div>

                        <div class="row flex-row-reverse mt-2">
                            <div class="text-end text-right col-12">
                                <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>Save</button>
                                <button @click="$emit('close')" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>Cancel</button>
                            </div>

                        </div>

                    </form>
                </ValidationObserver>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    defineComponent,
    onMounted,
    reactive,
    ref
} from '@vue/composition-api'
import {
    userTimerInfo
} from "../../../stores/TimeTrack.js"
import {
    storeToRefs
} from 'pinia';
import DataService from '../../../services/DataService';
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
extend('required', {
    // validate(value) {
    //     return {
    //         required: true,
    //         valid: ['', null, undefined].indexOf(value) === -1
    //     };
    // },
    // message: '{_field_} is required',
    // computesRequired: true
});
export default defineComponent({
    components: {
        ValidationProvider,
        ValidationObserver,
    },
    setup() {
        const store = userTimerInfo();
        const {
            selectedValues
        } = storeToRefs(store)

        return {
            store,
            selectedValues

        }
    },
    props: {
        selectedRecordValues: {
            type: Array,
            required: true
        },
        AssignDpt: {
            type: Array,
            required: true
        }

    },
    data() {
        return {
            FormSchema: [{
                    layoutType: "single",
                    Data: [{
                        astype: "SelectField",
                        label: this.$t('Project'),
                        name: "project_id",
                        value: 0,
                        placeholder: "",
                        config: {
                            options: [],
                            onChange: this.onChangeDropdown
                        },
                        validationRules: {
                            //"required": true
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "SelectField",
                        label: this.$t('Task'),
                        name: "task_id",
                        value: 0,
                        placeholder: "",
                        config: {
                            options: [],
                        },
                        validationRules: {
                            //"required": true
                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "CustomSelectField",
                        label: this.$t('Tag'),
                        name: "tag_ids",
                        value: '',
                        mode: "tag",
                        placeholder: "",
                        config: {
                            options: [],

                        },
                    }]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "ToggleField",
                        label: this.$t('Billing'),
                        name: "IsProjectBillable",
                        value: false,
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],

                        },
                    }, ],
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "track_description",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            //"required": true
                        },
                        config: {
                            options: [],

                        },
                    }, ],
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup,
                },
            ],
            projectEdit: '',
            taskEdit: '',
            tagsEdit: '',
            paidEdit: '',
            descriptionEdit: '',
            bulkData: {
                projectId: '',
                taskId: '',
                tagsId: '',
                isBillable: false,
                trackDescription: ''
            },
            tagSelect: []
            // DataLoaded: 0,
            //TagsSelectedArray: [],
            //departmentList: [],
            //newDepartmentlist: [],
            //ListData: [],
            //TagsSelectedArray1: [],
            //tagsData: [],
            //TagsSelectedArray2: [],
            //TagsSelectedArray3: [],
            // MultiArray: [],
            //obj: ''
        }
    },
    created: function () {
        //this.GetCategoryList();
        //this.GetTimerProjectsByUserID();
        //this.LoadTask();
        //this.GetCompanyTaglist();
        //this.DataLoaded = 1;
        //this.pushList();
        //this.FetchData(this.AssignDpt);
    },
    methods: {
        taskByProject() {
            console.log("check")
            if (this.bulkData.projectId) {
                this.store.loadTasks(this.bulkData.projectId.projectId)
            } else {
                //getTasksFromProject = []
            }
            this.store.$state.tasks = []
        },
        closePopup() {
            this.$emit('close')
        },

        async onSubmit(formfields, e) {
            var vm = this;
            var trackid = this.store.$state.selectedValues.toString();
            var obj = {}
            obj.track_id = trackid
            if (vm.projectEdit) {
                obj.project_id = typeof vm.bulkData.projectId == 'object' ? vm.bulkData.projectId.projectId : vm.bulkData.projectId
            }
            if (vm.taskEdit) {
                obj.task_id = typeof vm.bulkData.taskId == 'object' ? vm.bulkData.taskId.value : vm.bulkData.taskId
            }
            if (vm.tagsEdit) {
                obj.tag_ids = ''
                this.tagSelect.forEach((item, index) => {
                    obj.tag_ids += index != 0 ? ',' + item.tagId : item.tagId
                });

            }
            if (vm.paidEdit) {
                obj.isBillable = vm.bulkData.isBillable
            }
            if (vm.descriptionEdit) {
                obj.track_description = this.bulkData.trackDescription
            }

            vm.isLoading = true;
            if (vm.projectEdit || vm.taskEdit || vm.tagsEdit || vm.paidEdit || vm.descriptionEdit) {
                await DataService.EditBulkTimeTrackEntry(obj).then(response => {
                    vm.isLoading = false;
                    if (response.data.status == 0) {
                        vm.ShowAlert(vm.$t('Track Entries is updated'), "success", false, vm.$t("Alert"));
                        const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                        this.store.getTimerListing1(url)
                        this.store.$state.selectedValues = []
                        this.store.$state.expendListRow = false
                        this.$emit('close')
                    }
                    //vm.$parent.FetchData();

                })
            } else {
                vm.ShowAlert(vm.$t('SelectCheckbox'), "danger", false, vm.$t("Alert"));
            }

        }
    }

})
</script>
