<template>
<section class="activity-tab col-md-12 p-0 mt-4 float-left">
    <div>

        <!-- <button class="tablinks" @click="teamStatus">{{ $t('MyStatus') }}</button>
<button class="tablinks" @click="myStatus">{{ $t('Teamstatus') }}</button> -->

        <div class="tab_container tab-style newtimer">
            <div class="responsive-tabs-container accordion-xs accordion-sm">
                <ul id="myTab" role="tablist" class="nav nav-tabs all-tabs responsive-tabs mt-2 responivecustomtab HideTab w-100">
                    <li class="nav-item">
                        <a @click="myStatus" data-toggle="tab" role="tab" id="tab-Details" class="nav-link productCreateTab1 active" aria-selected="true"><em class="fa fa-user mr-1"></em>
                            {{ $t('MyStatus') }}</a>
                    </li>
                    <li class="nav-item">
                        <a @click="myStatus" data-toggle="tab" role="tab" id="tab-Details" class="nav-link productCreateTab1" aria-selected="true"><em class="fa fa-users mr-1"></em>
                            {{ $t('Teamstatus') }}</a>
                    </li>
                </ul>
            </div>
        </div>

    </div>

    <div v-if="myStatusShow" class="col-lg-12 p-0 float-left" id="week-status">
        <div class="row weeklystatusboxcontrol">
            <div class="col-md-4 mb-3">
                <div class="border calbx eqcol h-100">
                    <div class="month-heading" style="position: relative;">
                        <h3>{{ formatOnlyMonthYearWithComma(WeekDate) }}</h3>
                        <div class="btnbase">
                            <a href="javascript:void(0)" v-on:click="CounterDecrement('-', WeekDayCount, WeekDayCount += 1)" class="btnsnextprev"><em class="fa fa-angle-double-left mr-1"></em>Previous</a><a href="javascript:void(0)" v-if="WeekDayCount > 0" v-on:click="CounterIncrement('-', WeekDayCountInCrease += 1)" class="btnsnextprev">{{$t('Next')}}<em class="fa fa-angle-double-right ml-1"></em></a>
                        </div>
                    </div>
                    <div class="month_day">
                        <ul>
                            <li class="d-none">{{ TimeTrackList.length }}</li>
                            <li v-for="(trackGroup, indexTimeTrack)  in TimeTrackList" :key="indexTimeTrack">
                                <div class="date_fm">
                                    <h4>{{ formatOnlyDate(trackGroup.WeekDate) }}</h4>
                                    <p>{{ formatOnlyDay(trackGroup.WeekDate) }}</p>
                                </div>

                                <div v-if="indexTimeTrack == 5 && trackGroup.TotalHours == null" v-bind:class="{ 'darkyellow': indexTimeTrack == 5 && trackGroup.TotalHours == null }" class="lightblue">
                                    <p>H</p>
                                </div>

                                <div v-else-if="indexTimeTrack == 6 && trackGroup.TotalHours == null" v-bind:class="{ 'lightyellow': indexTimeTrack == 6 && trackGroup.TotalHours == null }" class="lightblue">
                                    <p>H</p>
                                </div>

                                <div v-else class="lightblue">
                                    <h6>{{$t('TimeTrack')}}</h6>
                                    <h6>{{ trackGroup.TotalHours > 0 ? convertSecondsToHHMMSS(trackGroup.TotalHours) : 0
                                        }}</h6>
                                    <h6>hours</h6>
                                </div>
                            </li>

                        </ul>
                    </div>

                </div>
            </div>
            <div class="col-md-4 mb-3">
                <div class="border infotrack eqcol h-100 row">

                    <div class="trackinfo">
                        <h2 class="time_info">{{ convertSecondsToHHMMSS(TotalWeekHours) }} {{$t('HrsSmall')}}</h2>
                        <h3>{{ $t('TotalTimeTracked') }}</h3>
                        <p class="mb-4">Avg. Per Day -
                            {{ convertSecondsToHHMMSS(calculateWeekHoursAVG(TotalWeekHours, WeekDays)) }} {{$t('HrsSmall')}}</p>
                        <div class="containerLegendTimeTrack containerLegend100_WS uniquefixHS w-100"></div>
                        <div id="chart" v-if="TotalWeekHours">

                            <VueApexCharts type="bar" height="150" :options="chartOptionsBar" :series="seriesbar">
                            </VueApexCharts>
                        </div>

                    </div>
                    <div class="col-img text-center track-hours-chart" v-if="chartOptions">
                        <VueApexCharts type="pie" width="800" :options="chartOptions" :series="series" />
                    </div>
                </div>
            </div>
            <div class="col-md-4 mb-3">

                <div class="border infotrack eqcol h-100">

                    <div class="trackinfo">
                        <ul>
                            <li class="txt_overdue"><span>{{ OverdueRecords }}</span> {{$t('OVERDUE')}} </li>
                            <li class="txt_pending"><span>{{ PendingRecords }}</span> {{$t('Pending')}} </li>
                            <li class="txt_inprogress"><span>{{ ProgressRecords }}</span> {{$t('Inprogress')}} </li>
                            <li class="txt_progress"><span>{{ CompletedRecords }}</span> {{$t('Completed')}} </li>
                            <li class="txt_to-upcoming"><span>{{ UpcomingRecords }}</span>{{$t('Upcoming')}} </li>
                        </ul>
                    </div>
                    <div class="col-img text-center track-hours-chart" v-if="chartOptions1">
                        <VueApexCharts type="pie" width="800" :options="chartOptions1" :series="series1" />
                    </div>
                </div>
            </div>

        </div>
    </div>
    <div v-else class="col-lg-12 p-0 float-left">

        <div class="container-fluid">

            <div class="border bg-light p-3 float-left w-100 col-md-12 mt-2">
                <div class="col-12 float-left p-0" v-for="(item, index) in store.getTimerTeamStatusState.TRACKTIME" :key="index">
                    <div class="leftstatus">

                        <div class="col-12 float-left border bg-light p-0 mb-3">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-6 float-left bg-white team-con-box">
                                <div class="team-con-img">
                                    <img class="img-circle avatar" src="@/assets/avtar.jpg" width="70" height="70" alt="Image">
                                    <div class="online"></div>
                                </div>
                                <div class="team-con-content">
                                    <div class="names font-weight-medium">{{ item.userName }} <em class="fa fa-globe ml-1"></em></div>

                                    <div class="col-md-12 float-left mt-2 p-0">

                                        <div class="col-md-3 float-left p-0">
                                            <div class="barsbase w-100 float-left weektracked">
                                                <div class="iconbase"><em class="fa fa-clock-o"></em></div>
                                                <div class="icontext">
                                                    <span class="w-100 float-left font-weight-medium font-14">{{ item.TrackTimer }}m</span>
                                                    <span class="w-100 float-left font-12"> {{$t('Weeklytracked')}} </span>
                                                </div>

                                            </div>
                                        </div>

                                        <div class="col-md-3 float-left p-0">
                                            <div class="barsbase w-100 float-left auto">
                                                <div class="iconbase"><em class="fa fa-circle-o-notch"></em></div>
                                                <div class="icontext">
                                                    <span class="w-100 float-left font-weight-medium font-14">{{ item.Automatic }}%</span>
                                                    <span class="w-100 float-left font-12">Auto</span>
                                                </div>

                                            </div>
                                        </div>

                                        <div class="col-md-3 float-left p-0">
                                            <div class="barsbase w-100 float-left manual">
                                                <div class="iconbase"><em class="fa fa-clock-o"></em></div>
                                                <div class="icontext">
                                                    <span class="w-100 float-left font-weight-medium font-14">{{ item.Manual }}%</span>
                                                    <span class="w-100 float-left font-12">{{$t('Manual')}}</span>
                                                </div>

                                            </div>
                                        </div>

                                        <div class="col-md-3 float-left p-0">
                                            <div class="barsbase w-100 float-left efficiency">
                                                <div class="iconbase"><em class="fa fa-clock-o"></em></div>
                                                <div class="icontext">
                                                    <span class="w-100 float-left font-weight-medium font-14">{{ item.Efficiency }}%</span>
                                                    <span class="w-100 float-left font-12">{{$t('Efficieny')}}</span>
                                                </div>

                                            </div>
                                        </div>
                                        <div class="progress w-100">

                                            <div class="progress-bar" role="progressbar manual" :style="'width: '+item.Automatic+'%;background-color: #f7a82f;'" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>

                                            <div class="progress-bar" role="progressbar" :style="'width:'+item.Manual+'%;background-color: #00b298;'" aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">
                                            </div>
                                        </div>

                                    </div>

                                </div>
                            </div>

                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-6 float-left">
                                <div class="team-con-rangecontent mt-4">
                                    <span class="w-100 float-left font-weight-medium">{{ item.ProjectName }}</span>
                                    <span class="w-100 float-left">{{ item.TaskDetail }}</span>
                                </div>
                                <div class="team-con-range mt-2">{{ item.TrackTime }}</div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-6 float-left">
                                <div id="chart">
                                    <VueApexCharts type="donut" :options="chartOptions2[index]" :series="series2[index]"></VueApexCharts>
                                </div>
                            </div>

                        </div>

                    </div>

                    <div class="rightgraph bg-white border">
                        <div class="eqcol h-100 row p-4">
                            <div class="trackinfo">
                                <h2 class="time_info">{{ convertSecondsToHHMMSS(TotalWeekHours) }} Hrs</h2>
                                <h4>{{ $t('TotalTimeTracked') }}</h4>
                                <p class="mb-4">{{ $t('AvgPerDay') }}
                                    {{ convertSecondsToHHMMSS(calculateWeekHoursAVG(TotalWeekHours, WeekDays)) }} Hrs
                                </p>
                                <div class="containerLegendTimeTrack containerLegend100_WS uniquefixHS w-100"></div>
                                <div id="chart" v-if="TotalWeekHours">

                                    <VueApexCharts type="bar" height="150" :options="chartOptionsBar" :series="seriesbar">
                                    </VueApexCharts>
                                </div>

                            </div>
                            <div class="col-img text-center track-hours-chart" v-if="chartOptions">
                                <VueApexCharts type="pie" width="600" :options="chartOptions" :series="series" />
                            </div>
                        </div>
                    </div>

                </div>

            </div>

        </div>

    </div>

</section>
</template>

<script>
import DataService from '@/services/DataService';
import moment from 'moment';
import {
    formatter,
    convertSecondToHHMM
} from "@/assets/common/vue-common.js"
import VueApexCharts from 'vue-apexcharts'
export default {

    components: {
        VueApexCharts
    },
    props: {
        store: {
            type: Object
        },
    },

    data() {
        return {
            TimeTrackList: [],
            WeekDayCount: 0,
            WeekDayCountInCrease: 0,
            WeekDayInCreaseDecrease: 0,
            isActiveWeeklyStatus: true,
            PROGRESS: '',
            PendingRecords: 0,
            ProgressRecords: 0,
            OverdueRecords: 0,
            UpcomingRecords: 0,
            CompletedRecords: 0,
            chartData: [],
            pieCharLable: [],
            series: [],
            series1: [],
            series2: [],
            chartOptions: {

            },
            chartOptions1: {

            },
            chartOptions2: [],
            seriesbar: [],
            chartOptionsBar: {},
            taskList: [],
            myStatusShow: true,
            teamStatus: [],
            projectDetails: []

        }
    },
    created() {
        debugger;

        this.TimeTrackWeeklyDataList();
        this.TimerWeeklyTask();
        this.TimerWeeklyTeamStatus();
        console.log("state", this.store.$state.dateFilterType)

    },
    methods: {
        toggleWeeklyStatus() {
            if (!this.isActiveWeeklyStatus) {
                this.isActiveWeeklyStatus = true;
            } else {
                this.isActiveWeeklyStatus = false;
            }
        },
        formatOnlyMonthYearWithComma: function (value) {
            //debugger;
            if (value) {
                return moment(value).format('MMM') + ', ' + moment(value).format('YYYY');
            }
        },
        formatOnlyDay: function (value) {

            if (value) {
                // return moment(value).format('ddd').toUpperCase();
                return moment(value).format('ddd').toUpperCase();
            }
        },
        CounterDecrement: function (value) {
            var vm = this;
            vm.WeekDayInCreaseDecrease = value + vm.WeekDayCount;
            vm.TimeTrackWeeklyDataList();
        },
        CounterIncrement: function (value) {
            var vm = this;
            var increaseValue = parseInt(vm.WeekDayCount) - parseInt(vm.WeekDayCountInCrease);
            vm.WeekDayInCreaseDecrease = value + increaseValue;
            if (String(vm.WeekDayInCreaseDecrease).indexOf("--") !== -1) {
                vm.WeekDayInCreaseDecrease = 0;
            }
            if (vm.WeekDayInCreaseDecrease == 0) {
                vm.WeekDayCount = 0;
            }
            vm.TimeTrackWeeklyDataList();
        },
        convertSecondsToHHMMSS: function (intSecondsToConvert) {
            if (intSecondsToConvert > 0) {
                return formatter(intSecondsToConvert)
            } else {
                return "00:00:00"
            }

        },
        formatOnlyDate: function (value) {
            if (value) {
                return moment(value).format('DD');
            }
        },
        calculateWeekHoursAVG: function (totalHours, days) {
            var avgPerWeekHours = Math.floor(totalHours / days);
            if (isNaN(avgPerWeekHours)) {
                avgPerWeekHours = 0;
            }
            return avgPerWeekHours
        },
        async TimeTrackWeeklyDataList() {
            var vm = this;
            //showLoader();
            var url = "weekDay=" + vm.WeekDayInCreaseDecrease
            let seriesbarData = [{
                "name": "Time",
                data: []
            }]
            let seriesData = []
            let seriesDonutData = []
            let data = []
            let pieCharLableData = []
            DataService.TimeTrackWeeklyDataList(url).then(response => {
                if (response.data != null && response.data != "") {
                    vm.noRecordToDoList = false;
                    vm.TimeTrackList = [];
                    vm.TimeTrackList = response.data.result.DATA;
                    var sum = 0;
                    var WeekDays = 0;
                    var TimeTrackListNew = [];
                    //vm.seriesbar = []
                    vm.TimeTrackList.map((item, index) => {
                        var objWeekDay = {};
                        if (item.TotalHours != null) {
                            sum = parseInt(sum) + parseInt(item.TotalHours);
                            vm.WeekDays = index + 1;
                            vm.WeekDate = item.WeekDate;
                            var weekDay = vm.formatOnlyDay(item.WeekDate);
                            objWeekDay = {
                                Name: weekDay,
                                Value: item.TotalHours,
                            };
                            // TimeTrackListNew.push(objWeekDay);
                            seriesData.push(objWeekDay.Value);
                            seriesbarData[0].data.push(objWeekDay.Value);
                            //vm.seriesbar.push(objWeekDay);
                            pieCharLableData.push(objWeekDay.Name)

                        }
                    })

                    //console.log("test",vm.WeekDate,vm.WeekDays)
                    vm.TotalWeekHours = sum;
                }
            });
            this.pieChart(pieCharLableData, seriesData)
            this.lineChart(pieCharLableData, seriesbarData)

        },
        async TimerWeeklyTask() {
            var vm = this;
            let keys = [];
            let values = [];
            DataService.TimerWeeklyTask().then(response => {

                if (response.data != null && response.data != "") {
                    debugger
                    // console.log("data",response.data.result)
                    vm.OverdueRecords = response.data.result.OVERDUERECORDS;
                    vm.CompletedRecords = response.data.result.COMPLETEDRECORDS;
                    vm.PendingRecords = response.data.result.PENDINGRECORDS;
                    vm.ProgressRecords = response.data.result.PROGRESSRECORDS;
                    vm.UpcomingRecords = response.data.result.UPCOMINGRECORDS
                    // data = [vm.OverdueRecords,vm.CompletedRecords,vm.PendingRecords,vm.ProgressRecords,vm.UpcomingRecords]
                    // label=['OverdueRecords','CompletedRecords',PendingRecords,ProgressRecords,UpcomingRecords]
                    keys = Object.keys(response.data.result);
                    values = Object.values(response.data.result);
                    console.log("Keys:", keys);
                    console.log("Values:", values);
                }
                this.pieChart1(keys, values)

            });

        },
        donutChart(series, index, data, lable) {

            this.series2[index] = series
            this.chartOptions2[index] = {
                chart: {
                    width: 280,
                    type: 'donut',

                },
                stroke: {
                    width: 0,
                },
                plotOptions: {
                    pie: {
                        donut: {
                            labels: {
                                show: true,
                                name: {
                                    show: false,
                                },
                                total: {
                                    show: true,
                                    showAlways: true,

                                    formatter: function () {
                                        return convertSecondToHHMM(data)
                                    }
                                }

                            }
                        }
                    }
                },

                labels: lable,
                dataLabels: {
                    enabled: false,
                    dropShadow: {
                        blur: 3,
                        opacity: 0.8
                    }
                },

                responsive: [{
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 100
                        },
                        legend: {
                            position: 'bottom'
                        }
                    }
                }]
            };

        },

        pieChart(lable, series) {

            this.series = series
            this.chartOptions = {
                chart: {
                    width: 350,
                    type: 'pie',
                },
                labels: lable,
                tooltip: {
                    enabled: true,
                    y: {
                        formatter: function (val) {
                            return convertSecondToHHMM(val)
                        },
                        title: {
                            formatter: function (seriesName) {
                                return seriesName
                            }
                        }
                    }
                },
                responsive: [{
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 400
                        },
                        legend: {
                            position: 'top'
                        }
                    }
                }]
            }
        },
        pieChart1(label, series) {

            this.series1 = series
            this.chartOptions1 = {
                chart: {
                    width: 350,
                    type: 'pie',
                },
                labels: label,
                responsive: [{
                    breakpoint: 480,
                    options: {
                        chart: {
                            width: 400
                        },
                        legend: {
                            position: 'top'
                        }
                    }
                }]
            }
        },

        lineChart(lable, series) {
            let colors = ['#546E7A', '#66DA26', '#546E7A', '#E91E63', '#FF9800', '#FF9800', '#FF9800']
            let i = 0;
            this.seriesbar = series
            this.chartOptionsBar = {
                chart: {
                    height: 350,
                    type: 'bar',
                },
                plotOptions: {
                    bar: {
                        distributed: true,
                        borderRadius: 10,
                        dataLabels: {
                            position: 'top', // top, center, bottom
                        },
                    }
                },
                colors: ['#546E7A', '#66DA26', '#546E7A', '#E91E63', '#FF9800', '#FF9800', '#FF9800'],
                dataLabels: {
                    enabled: true,
                    formatter: function (val) {
                        return convertSecondToHHMM(val);
                    },
                    offsetY: -20,
                    style: {
                        fontSize: '12px',
                        colors: ["#304758"]
                    }
                },
                xaxis: {
                    categories: lable,
                    position: 'bottom',
                    axisBorder: {
                        show: false
                    },
                    axisTicks: {
                        show: false
                    },

                    tooltip: {
                        enabled: true,
                    }
                },

                yaxis: {
                    axisBorder: {
                        show: false
                    },
                    axisTicks: {
                        show: false,
                    },

                    labels: {
                        show: false,
                        formatter: function (val) {
                            return convertSecondToHHMM(val);
                        },

                    }

                },
            }
        },
        myStatus() {
            this.myStatusShow = this.myStatusShow = !this.myStatusShow
        },
        async TimerWeeklyTeamStatus() {
            var params = `dateType=${this.store.$state.dateFilterType}&toDate=${this.store.$state.To}&fromDate=${this.store.$state.From}`
            await this.store.TimerTeamStatus(params)
            this.store.getTimerTeamStatusState.TRACKTIME.map((item, index) => {
                this.donutChart([item.TrackHours, item.estimated_hours], index, item.TrackHours, ['Total Hours', this.$t('Efficieny')])
            })

        }

    }

}
</script>

<style>
.apexcharts-legend-series {
    display: none !important;
}
</style>
