<template>

    <div>
        <AddManualTimer v-if="addManualTimeOn" v-on:addManualTime="addManualTimeOn=false" v-bind:store="store"></AddManualTimer>
        <Timetrack v-else-if="!addManualTimeOn && timerStartFromList" v-on:addManualTime="addManualTimeOn=true" v-bind:store="store"></Timetrack>
        <Activities v-on:graphView="showListOrGraph" v-bind:store="store"></Activities>
        <GraphView v-if="graphShow" v-bind:store="store"/>
        <TimeTrackListing v-else v-on:timerClose="changeTimer" v-on:timerOpen="changeTimer" v-bind:store="store"></TimeTrackListing>
    </div>
</template>
<script>
import { defineComponent} from '@vue/composition-api'
import Timetrack from "../../components/TimeTrack/AddTimer.vue"
import AddManualTimer from "../../components/TimeTrack/AddManualTimer.vue"
import TimeTrackListing from "../../components/TimeTrack/TimeTrackListing.vue"
import  Activities from "../../components/TimeTrack/Activities.vue"
import GraphView from "../../components/TimeTrack/TimerGraphview.vue"
import { userTimerInfo } from "../../stores/TimeTrack.js"

export default defineComponent({
    components: {
        Timetrack,
        TimeTrackListing,
        Activities,
        AddManualTimer,
        GraphView
    },
    data(){
        return{
            addManualTimeOn:false,
            timerStartFromList:true,
            graphShow:false
        }
    },
    methods: {
        showListOrGraph(e){
          this.graphShow = e
        },
        changeTimer(){
            this.timerStartFromList = this.timerStartFromList =!this.timerStartFromList
        }
    },
    setup(){
        const store = userTimerInfo()
        return {
            store
        }
    }
})
</script>
