<template>
    <section>
        <div class="container-fluid p-0">
            <div class="col-md-12 p-0 mt-2">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('trackTime') }}</span>
                    <span class="p-actions float-right">
                        <a id="AddManually" href="javascript:;" class="p-action-btn text-white addman btn-info" :class="{'tt_disabled':timerOrBreak  }"
                            @click="$emit('addManualTime', true)"><i class="fa fa-clock-o mx-2"></i><span>{{ $t('AddedManually') }}</span></a>
                    </span>
                </div>
            </div>
            <ValidationObserver ref="form" v-slot="{ handleSubmit }">

                <form v-on:submit.prevent="handleSubmit(submit)" ref="form" :class="store.getTimer.trackAgent=='silentapp' ? 'tt_disabled' : ''">
                    <div class="border bg-light p-3 newtimetrack mt-2 float-left w-100">
                        <div class="basebod bg-white float-left w-100">
                            <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left leftcont_divs">
                                <div class="row">
                                    <multiselect :placeholder="$t('searchProject')" 
                                        class="form-group tt_ddr_1 col border-0 tt_bod-right" @input="taskByProject"
                                        v-model="trackerData.projectId" :options="getProjectsByUser" label="projectName"
                                        track-by="projectId" :show-labels="false" id="exampleFormControlSelect1"
                                        :class="store.getProjectDetails.trackType=='Break' ? 'tt_disabled':''">
                                    </multiselect>
                                    <multiselect  :placeholder="$t('searchTask')"
                                        class="form-control tt_ddr col border-0 tt_bod-right" id="exampleFormControlSelect2"
                                        v-model="trackerData.taskId" :options="getTasksFromProject" label="firstAttribute"
                                        track-by="value" :show-labels="false" @input="isBillableSatus" :class="store.getProjectDetails.trackType=='Break' ? 'tt_disabled':''">
                                        <template slot="option" slot-scope="props" :disabled="true">
                                            <!-- parentValue -->
                                            <div class="option__desc"><span class="option__title" :disabled="true">{{
                                                props.option.firstAttribute }}</span> 
                                                  <label class="float-right text-light px-2 ml-1 taskcount" >{{ props.option.subTasksCount }}</label>
                                                <label class="float-right"> {{ props.option.thirdAttribute }}% </label>
                                              </div>
                                        </template>
                                        
                                    </multiselect>
                                    <div class="p-0">
                                        <a class="tt_p-action-btn tt_bod-right" :class="store.getProjectDetails.trackType=='Break' ? 'tt_disabled':''"
                                            @click="tagShow = tagShow = !tagShow;tagOpenModal(); breakModalPopup = false"
                                            ref="tags">
                                            <i class="tt_icon ico_tag"></i><span class="text-uppercase"> {{ tagSelect.length
                                                > 0 ? tagSelect.length + ' '+$t('selected') : $t('tags') }}</span>
                                        </a>

                                        <a class="tt_p-action-btn tt_bod-right " :class="store.getProjectDetails.trackType=='Break' ? 'tt_disabled':''"
                                            @click="trackerData.isBillable = !trackerData.isBillable"
                                           >
                                            <i
                                                :class="trackerData.isBillable ? 'tt_icon ico_dollar' : 'tt_icon ico_dollar-o'"></i><span
                                                class="text-uppercase">{{ trackerData.isBillable ? 'Paid' : 'Unpaid' }}</span>
                                        </a>
                                        <a ref="break" class="tt_p-action-btn tt_bod-right"
                                            @click="breakModalOpen({ breakTimer: false, modalClose: true }); tagShow = false"
                                            :class="store.getTimer.enableBreak == 'yes' ? store.getProjectDetails.trackType=='Break' ? 'tt_disabled' : '' : 'd-none'">
                                            <i class="tt_icon ico_cutlery"></i><span class="text-uppercase">{{ $t('Break') }}</span>
                                        </a>

                                        <div class="hs_tag tagsModals" v-if="tagShow" ref="tagsModals">
                                            <multiselect ref="tagsMultiselect"  :close-on-select="false" :placeholder="$t('selectTags')" :multiple="true"
                                                class="form-group tt_ddr_1 col border-0 tt_bod-right" v-model="tagSelect"
                                                :options="store.getTags" label="tagName" track-by="tagId"
                                                :show-labels="false" id="exampleFormControlSelect1">
                                            </multiselect>
                                            <div data-v-0f960004="" class="ml-2 squarebtn"><a data-v-0f960004=""
                                                    class="round-icon-small btn-info border-radius-0" @click="openTagsAdd(true)"><i
                                                        data-v-0f960004="" alt="+"
                                                        class="fa fa-plus text-white pt-7 font-18"></i></a></div>

                                        </div>

                                        <div class="hs2 breakModal" v-if="breakModalPopup" ref="breakModal">
                                            <BreakModal ref="breakModal" v-if="breakModalPopup"
                                                v-on:close="breakModalPopup =false"
                                                v-bind:userId="store.getUserInformation.id" v-bind:timerModule="true"
                                                v-bind:trackDataPouse="trackerData" v-bind:automaticBreak="true">
                                            </BreakModal>
                                        </div>
                                    </div>

                                </div>

                            </div>

                            <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left rightcont_divs">
                                <div class="row">
                                    <ValidationProvider :rules="trackerData.projectId ? '' : 'required'" v-slot="{ errors }"
                                        class="tt_textarea_span position-relative  "
                                        :class="timerOrBreak && store.getTimer.enableBreak != 'yes' ? 'tt_textarea_stop' : timerOrBreak ? '' : ''">
                                        <textarea v-if="store.getProjectDetails.trackType!='Break'" id="Taskdescription" name="Description"
                                            v-model="trackerData.trackDescription" class="form-control tt_textarea"
                                            :class="errors[0] ? 'tt_textarea is-invalid' : timerOrBreak ? 'tt_textarea-stop' : 'tt_textarea'"
                                            style="resize:none" v-on:keydown="textCounter" v-on:keyup="textCounter"
                                            v-on:mouseout="textCounter" :placeholder="$t('PleaseEnterDescription')"
                                            maxlength="2000" @change="descriptionChange"></textarea>
                                            <textarea v-else id="Taskdescription" name="Description"
                                            v-model="store.getProjectDetails.breakName" class="form-control tt_textarea tt_disabled"
                                            :class="errors[0] ? 'tt_textarea is-invalid ' : timerOrBreak ? 'tt_textarea-stop' : 'tt_textarea'"
                                            style="resize:none" v-on:keydown="textCounter" v-on:keyup="textCounter"
                                            v-on:mouseout="textCounter" :placeholder="$t('PleaseEnterDescription')"
                                            maxlength="2000"></textarea>
                                        <span class="val_msg">{{ errors[0] ? $t('DescRequired') : '' }}</span>
                                    </ValidationProvider>
                                    <div class="tt_play_btnbase" v-if="store.getProjectDetails.trackType==null">
                                        <button class="tt_play_btn tt_bod-right" type="submit">
                                            <i class="fa fa-play text-white"></i>
                                        </button>
                                    </div>
                                    <div class="tt_play_btnbase" v-else>
                                        <a v-if="store.getProjectDetails.statusCode=='ACT' && store.getTimer.enableBreak != 'yes'"
                                            class="tt_play_btn tt_bod-right mr-1 bg-info" @click="pouseTimer()">
                                            <i class="fa fa-pause text-white"></i>
                                        </a>
                                        <a v-if="store.getProjectDetails.statusCode=='PAUS' && store.getTimer.enableBreak != 'yes'"
                                            class="tt_play_btn tt_bod-right" @click="resumeTimer()">
                                            <i class="fa fa-play text-white"></i>
                                        </a>
                                        <a class="tt_play_btn tt_bod-right bg-danger" @click="stopTimer();"
                                            v-if="store.getProjectDetails.trackType!='Break'">
                                            <i class="fa fa-stop text-white"></i>
                                        </a>
                                        <a class="tt_play_btn tt_bod-right bg-danger" @click="stopBreakTime();" v-else>
                                            <i class="fa fa-stop text-white"></i>
                                        </a>
                                    </div>
                                    <div id="TimerCanves" class="DivTimer timerclck" :data-test="timerPlay"
                                        v-if="store.getProjectDetails.trackType=='TimeTrack'" :class="store.$state.timerCountInSec > 0 ? 'success' : ''">
                                        {{ getTimerClock }}
                                    </div>
                                    <div id="TimerCanves" class="DivTimer timerclck"
                                        :class="store.$state.breakTimerCountInSec < 0 ? 'danger' : 'success'"
                                        :data-test="timerPlay" v-else>
                                        {{ store.getBreakTimerClock }}
                                    </div>

                                </div>

                            </div>

                        </div>

                    </div>

                </form>
            </ValidationObserver>
        </div>
        <AddNewTags v-on:close="openTagsAdd(e)" v-if="addTagsModal" />
    </section>
</template>

<script>
import {
    defineComponent,
    reactive,
    ref,
    toRefs
} from '@vue/composition-api'
import {
    userTimerInfo
} from "../../stores/TimeTrack"
import {
    storeToRefs
} from 'pinia';
import moment from "moment"
import Multiselect from 'vue-multiselect'
import {
    getSecondsFromDate,
    timeToSeconds
} from "@/assets/common/vue-common.js"
import BreakModal from "../TimeTrack/modals/BreakModal.vue"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
import AddNewTags from "./modals/AddNewTags.vue"
extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    computesRequired: true
});
export default defineComponent({
    name: "Timer Add",
    components: {
        Multiselect,
        BreakModal,
        ValidationProvider,
        ValidationObserver,
        AddNewTags
    },
    data() {
        return {
            //open manual timer
            menualTime: false,
            breakTimer: "00:00:00",
            //check break timer on or off
            breakTimerOn: false,
            textCountertlength: 2000,
            submitted: false,
            startTimerShow: false,
            timer: "00:00:00",
            error: "",
            timerStart: false,
            timerPouse: false,
            //Check time or break on 
            timerOrBreak: false,
            timerPlay: false,
            breakModalPopup: false,
            formData: {},
            tagShow: false,
            tagSelect: [],
            addTagsModal: false
        }
    },
    created() {
        this.getInComplete();
    },
    mounted() {
        //This script use for tags and break modal auto off when user click any other actions
        document.addEventListener("click", (e) => {
            if (this.tagShow) {
                if (e.target == this.$refs.tags || e.target.parentNode == this.$refs.tags || e.target.parentNode.closest(".tagsModals") == this.$refs.tagsModals || e.target == this.$refs.tagsModals) return
                this.tagShow = false
            }
            if (this.breakModalPopup) {
                if (e.target == this.$refs.break || e.target.parentNode == this.$refs.break || e.target == this.$refs.breakModal || e.target.parentNode.closest(".breakModal") == this.$refs.breakModal) { return }
                else {
                    this.breakModalPopup = false
                }
            }
        })

    },
    unmounted() {
        this.store.$state.tasks = []
        this.store.$state.timerRunResponse = ''
        this.error = ''
    },
    methods: {
        //When Component render then call
        async getInComplete() {
            this.store.getBreakTime();
            this.store.tagGetForTimer()
            //set blank object 
            Object.assign(this.formData, this.trackerData);
            await this.store.getProjects()
            await this.store.getTimerInfo()
            this.store.$state.tasks = []
            this.store.$state.timerRunResponse = ''
            this.error = ''
            if (this.store.getTimer?.statusCode) {
                this.trackerData = Object.assign(this.trackerData, this.store.getProjectDetails)
                this.timerOrBreak = true
                if (this.store.getTimer?.trackType == 'Break') {
                    this.store.$state.breakNegative = true
                    let seconds = getSecondsFromDate({
                        startDate: this.store.getTimer?.dateStart,
                        endDate: moment()
                    }, 'seconds')
                    this.breakTimerOn = true
                    //Get second from beark remain break timer
                    this.store.$state.breakTimerCountInSec = timeToSeconds(this.store.getProjectDetails.remainingBreakDuration) - seconds
                    this.store.$state.timerCountInSec = parseInt(this.store.getProjectDetails.resumeHours)
                    this.startBreakTime();
                    this.trackerData.trackDescription = this.store.getProjectDetails.breakName
                } else {
                    let countResumeInSeconds = 0
                    this.store.$state.timerCountInSec = moment(new Date(this.store.getTimer?.dateStop), "DD/MM/YYYY HH:mm:ss").format("DD-MM-YYYY") == "01-01-1950" ?
                        getSecondsFromDate({
                            startDate: this.store.getTimer?.dateStart,
                            endDate: moment()
                        }, 'seconds') : countResumeInSeconds + parseInt(this.store.getTimer.resumeHours)
                    if (this.store.getTimer?.statusCode != "ACT") {
                        this.store.setTimerClock(this.store.$state.timerCountInSec);
                        this.timerPouse = true

                    } else {
                        this.timerPlay = true
                        this.store.startTimer()

                    }
                }
            }
            this.store.getProjectDetails.projectId ? this.projectId() : this.trackerData.projectId = null
            if (this.store.getProjectDetails.taskId) {
                await this.store.loadTasks(this.store.getProjectDetails.projectId);
                this.taskId()
            } else {
                this.trackerData.taskId = null
            }
        },
        //get task according to project id
        taskByProject() {
            if (this.trackerData.projectId) {
                this.store.loadTasks(this.trackerData.projectId?.projectId)
            } else {
                //getTasksFromProject = []
                console.log("check", store.$state)
            }
            if(this.store.getProjectDetails.statusCode){
                this.submit(true)
            }
            this.store.$state.tasks = []
        },
        //On and off Break moadal
        breakModalOpen(event) {
            this.breakModalPopup = this.breakModalPopup = !this.breakModalPopup
            this.breakTimerOn = event.breakTimer
            this.timerOrBreak = this.store.$state.timerCountInSec > 0 ? true : event.breakTimer
           
        },

        resumeTimer() {
            this.timerPlay = true
            this.trackerData.startTime = moment().format('YYYY-MM-DD HH:mm:ss')
            this.submitTimerInfo('resume')
            this.timerOrBreak = true
            this.store.startTimer()
        },
        pouseTimer() {

            this.trackerData.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
            this.trackerData.totalHours = this.store.$state.timerCountInSec
            this.submitTimerInfo('pause')
            this.store.stopTimer()
            this.timerPlay = false
        },
        //Timer stop ,pouse and start Information send to backend
        async submitTimerInfo(event) {
            this.trackerData.CreatedBy = this.store.getUserInformation.id
            this.trackerData.createdBy = this.store.getUserInformation.id
            this.trackerData.trackId = this.store.getTimer?.trackId
            this.trackerData.trackDescription = this.store.getTimer?.trackDescription
            this.trackerData.timerStatus = event,
            this.trackerData.stopBreak = moment().format('YYYY-MM-DD HH:mm:ss')
            this.trackerData.type = "TimeTrack",
            this.trackerData.trackType = "TimeTrack"
            this.trackerData.projectId = this.store.getTimer.projectId  ? this.store.getTimer.projectId : this.trackerData.projectId == null ? 0 : this.trackerData.projectId ? this.trackerData.projectId.projectId : this.trackerData.projectId
            this.trackerData.taskId = this.store.getTimer.taskId ? this.store.getTimer.taskId : this.trackerData.taskId == null ? 0 : this.trackerData.taskId ? this.trackerData.taskId.value : this.trackerData.taskId
            await this.store.submitProjectInfo(this.trackerData)
            //if(this.store.getTimerRunRespolnse())
            await this.store.getTimerInfo();
            this.projectId()
            this.taskId()
            if (event == 'stop') {
                Object.assign(this.trackerData, this.formData)
            }

        },
        async stopTimer() {
            this.trackerData.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
            this.trackerData.totalHours = this.store.$state.timerCountInSec
            await this.submitTimerInfo('stop')
            this.timerOrBreak = false
            this.trackerData.projectId = 0
            this.trackerData.taskId = 0
            this.trackerData.trackDescription = ''
            this.store.stopTimer()
            this.store.$state.timerCountInSec = 0
            this.store.$state.timerClock = "00:00:00"
            this.$refs.form.reset()
            this.tagSelect = []
            Object.assign(this.trackerData, this.formData)
            const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
            this.store.getTimerListing1(url)
        },
        startBreakTime() {
            //this.breakTimerShow = true
            //this.breakTimerPlay = true

            this.store.breakStartTimer()
        },
        //Stop Break
        async stopBreakTime() {
            this.trackerData.CreatedBy = this.store.getUserInformation.id
            this.trackerData.trackId = this.store.getTimer.trackId
            this.trackerData.trackDescription = this.store.getTimer?.trackDescription
            this.trackerData.timerStatus = "stop",
            this.trackerData.type = "Break"
            this.trackerData.BreakStartTime = this.store.getTimer.dateStart
            this.trackerData.createdBy = this.store.getUserInformation.id
            this.trackerData.BreakStopTime = moment().format('YYYY-MM-DD HH:mm:ss')
            let seconds = getSecondsFromDate({
                startDate: this.store.getTimer?.dateStart,
                endDate: moment()
            }, 'seconds')
            this.trackerData.totalHours = seconds
            this.store.breakStopTimer()
            this.breakTimerOn = false
            this.trackerData.projectId = this.trackerData.projectId ? this.trackerData.projectId.projectId : this.trackerData.projectId
            await this.store.submitProjectInfo(this.trackerData) 
            const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
            this.store.getTimerListing1(url)
            this.store.$state.timerRunResponse = ''
            this.store.$state.breakTimerCountInSec = 0
            this.timerOrBreak = false
            Object.assign(this.trackerData,this.formData);
            await this.store.getTimerInfo();   
            if(this.store.getProjectDetails.statusCode=="PAUS" && this.store.getProjectDetails.trackType=='TimeTrack'){
            this.store.$state.timerCountInSec = parseInt(this.store.getTimer.resumeHours)
            this.resumeTimer()

            }
            this.projectId()
            this.taskId()
            

        },
        //Submit Add Timer Form
        async submit(e) {
            if(e){
                this.trackerData.timerStatus="update"
            }
            this.submitted = true
            this.trackerData.projectId = this.trackerData?.projectId ? this.trackerData?.projectId?.projectId : 0
            this.trackerData.taskId = this.trackerData?.taskId ? this.trackerData?.taskId.value : 0
            this.trackerData.isBillable = this.trackerData?.isBillable ? 1 : 0
            this.trackerData.timeformat = this.trackerData?.format ? 24 : 12
            this.trackerData.stopBreak = null
            this.trackerData.TagIds = ''
            this.trackerData.startTime = moment().format("YYYY-MM-DD HH:mm:ss")
            this.trackerData.stopTime = moment().format("YYYY-MM-DD HH:mm:ss")
            this.tagSelect.map((items, index) => {
                this.trackerData.TagIds += index != 0 ? ',' + items.tagId : items.tagId
            })
            await this.store.submitProjectInfo(this.trackerData);
            this.startTimerShow = this.startTimerShow;
         
            if (this.store.getTimerRunResponse?.trackId) {
                
                if(e!=true){
                    //this.tagSelect = []
                    this.store.startTimer(0)

            }
                this.timerPlay = true
                this.submitted = false
                this.timerOrBreak = true
            } else {
                this.error = this.store.getTimerRunResponse?.userMsg;
            }
            await this.store.getTimerInfo();
            this.projectId()
            this.taskId()

        },
        descriptionChange(){
            if(this.store.getProjectDetails.statusCode){
                this.submit(true)
            }
        },
        //If project id get number from backend then set custom object for multydropdown
        projectId() {
            this.trackerData.projectId = this.store.getProjectDetails.projectId ? this.store.getProjectsByUser.find((data) => data.projectId == this.store.getProjectDetails.projectId) : null
        },
        //If task id get number from backend then set custom object for multydropdown
        taskId() {
            this.trackerData.taskId = this.store.getProjectDetails.taskId ? this.store.getTasksFromProject.find((data) => data.value == this.store.getProjectDetails.taskId) : null
        },
        //Open Add tags modal
        openTagsAdd(e) {
            this.addTagsModal = e
        },
        //Get isBillable status from task  is
        isBillableSatus(){
            this.trackerData.isBillable  = this.trackerData.taskId.isBillable
            if(this.store.getProjectDetails.statusCode){
                this.submit(true)
            }
           // console.log("checkk",this.trackerData.taskId.isBillable)
        },
        //When open tags select box then auto show select box options
        tagOpenModal(){
            setTimeout(()=>{
                this.$refs.tagsMultiselect.activate()
            },1)
            
        }
    },
    setup() {
        const store = userTimerInfo();
        const {
            getProjectDetails,
            getProjectsByUser,
            getTasksFromProject,
            getLoadingStatus,
            getUserInformation,
            getTimerRunResponse,
            getTimerClock,
            getTimer
        } = storeToRefs(store);
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: true,
            format: false,
            date: null,
            trackId: 0,
            startTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            stopTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            companyId: null,
            currentDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: getUserInformation.value.id,
            createdBy: getUserInformation.value.id,
            linkedTrackId: null,
            totalHours: 0,
            timeformat: "12",
            TagIds: '',
            isForcedUpdate: null,
            type: "TimeTrack",
            BreakDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            BreakName: null,
            BreakId: 0,
            BreakStartTime: 0,
            Shift_ID: 0,
            SelectedDate: "",
            Break_Shift: "",
            BreakStopTime: moment().format("YYYY-MM-DD HH:mm:ss")
        })

        //  console.log("projects",projects.value)

       
        // Description text length counter
        function textCounter() {
            //state.value.textCountertlength = 2000 - trackerData.trackDescription.length
        }

        return {
            trackerData,
            getTimerClock,
            getProjectsByUser,
            textCounter,
            getTasksFromProject,
            getLoadingStatus,
            getUserInformation,
            getTimerRunResponse,
            store,

        }
    },
})
</script>

<style>

.tt_ddr .taskcount{background-color: #525252; border-radius: 5px; padding: 2px 3px; min-width: 26px; text-align: center;}

@media (max-width:1200px)
{
.leftcont_divs{max-width: 55%;}
.rightcont_divs{max-width: 45%;}
.multiselect__option--selected{font-weight: 600;}
}

@media (max-width:1024px)
{
.leftcont_divs{max-width: 100%; border-bottom: 1px solid #cdcecf;}
.rightcont_divs{max-width: 100%;}
}

@media (max-width:767px)
{
.leftcont_divs{max-width: 100%; border-bottom: 1px solid #cdcecf;}
.leftcont_divs .multiselect {max-width: 100%; box-sizing: inherit; border-bottom: 1px solid #cdcecf!important;}

.leftcont_divs .row{display: block;}

.rightcont_divs{max-width: 100%;}

}


</style>