<template>
    <section>
        <div class="activity-tab col-md-12 p-0 mt-4 float-left" v-if="getTimerListing.timeListDateGroup.length == 0">
            <div id="accordion" class="w-100">
                <div class="card">
                    <div class="card-header">
                        <a >
                            <div class="conversation-date heading-border">
                                <span>{{$t('Today')}}  &nbsp;&nbsp;<em class="fa fa-angle-down rotate-icon pr-1"></em></span>
                            </div>
                        </a>
                    </div>
                    <div  id="collapseOne_index" class="collapse show">
                    <div class="card-body">
                        <div class="row">
                            <div class="col-md-12">
                                <div class="table-responsive">
                                    <table class="table table-bordered mt-0 mb-0 newtimertable">
                                        <tbody>
                                            <tr >{{$t('NoRecordfound')}}</tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                    </div>
                </div>
            </div>
        </div>
       
        <div class="activity-tab col-md-12 p-0 mt-4 float-left" v-for="(item, index) in getTimerListing.timeListDateGroup"
            :key="index">
            <!-- <div >
            <span @click="getListByDateType('thisweek')">Thisweek</span>
            <span @click="getListByDateType('thismonth')">ThisMonth</span>
        </div> -->
            <div id="accordion" class="w-100">
                <div class="card">
                    <div class="card-header">
                        <a @click="toggleCollapse(index)" class="card-link" :aria-expanded="isCollapsed(index)">
                            <div class="conversation-date heading-border">
                                <span>{{ getPreviousDay(item.dateGroup) }} &nbsp;&nbsp;<em
                                        class="fa fa-angle-down rotate-icon pr-1"></em></span>
                            </div>
                        </a>
                    </div>

                    <div v-if="!isCollapsed(index)" id="collapseOne_index" class="collapse show">
                        <div class="card-body">
                            <div class="row">
                                <div class="col-md-12">
                                    <div class="table-responsive">
                                        <table class="table table-bordered mt-0 mb-0 newtimertable">
                                            <tbody>
                                                <template v-for="(data, i) in item.entryList">
                                                    <tr :key="i"
                                                        :class="[rowClass(i), entryStatusClass(data.StatusCode), EntryTypeColor(data)]"
                                                        :trackId="data.trackId">
                                                        <td class="tabchild" style="max-width:10px;">

                                                            <div class="form-check form-check-inline"
                                                                v-if='data.statusCode == "TRACK_ENTRY_APPROVED" || data.statusCode == "TRACK_ENTRY_REJECTED" || data.statusCode == "TRACK_ENTRY_DELETE_PENDING" || data.approversCount != 0 || data.projectStatus == "INACT" || data.projectStatus == "DEL" || data.isLock == 1 || ((data.statusCode) == "TRACK_ENTRY_ADD_MANUAL_PENDING" && item.approvalTypeForTime == "Timesheet")'>
                                                                <div class="custom-control custom-checkbox">
                                                                    <input type="checkbox" :disabled="true"
                                                                        :class="{ 'chkItems custom-control-input': true }"
                                                                        :value="data.trackId" name="DeleteInputs"
                                                                        :id="'chk_' + data.trackId" @change="selectedValues"
                                                                        v-model="store.$state.selectedValues" style="display: none;">
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pt-1"
                                                                        :for="'chk_' + data.trackId"></label>
                                                                </div>

                                                            </div>
                                                            <div class="form-check form-check-inline" v-else>
                                                                <div class="custom-control custom-checkbox">
                                                                    <input type="checkbox"
                                                                        :class="{ 'chkItems custom-control-input': true }"
                                                                        :value="data.trackId" name="DeleteInputs"
                                                                        :id="'chk_' + data.trackId" @change="selectedValues"
                                                                        v-model="store.$state.selectedValues" style="display: none;">
                                                                    <label
                                                                        class="custom-control-label universal-custom-control-label pt-1"
                                                                        :for="'chk_' + data.trackId"></label>
                                                                </div>

                                                            </div>

                                                        </td>
                                                        <td style="max-width:150px">
                                                            
                                                            <a id="aParentID" v-if="isExpendCollapsed(data.trackId) && store.$state.expendListRow===true"
                                                                @click="expendToggleCollapse(data.trackId)"
                                                                :class="{ 'parentwithchild newtimetrack d-inline-block parent-childrow expandchild': data.trackLinkedId > 0, 'd-none': data.trackLinkedId <= 0 }"
                                                                href="javascript:;"><em class="fa fa-chevron-down"></em></a>
                                                                <a id="aParentID" v-else
                                                                @click="tracklocation(data.trackId + 'abc', data)"
                                                                :class="{ 'parentwithchild newtimetrack d-inline-block parent-childrow expandchild': data.trackLinkedId > 0, 'd-none': data.trackLinkedId <= 0 }"
                                                                href="javascript:;"><em class="fa fa-chevron-up"></em></a>
                                                            <div class="d-inline-block"  v-if="data.trackType!='Break'">
                                                                <a title="" value="-1" style="cursor:default;"
                                                                    class="text-dark" href="#">
                                                                    <span class="d-block font-14 font-weight-medium">{{
                                                                        data.projectName }}:{{ data.taskName }}</span>
                                                                    <span class="text-dark d-block font-12">{{
                                                                        data.trackDescription }}</span>
                                                                </a>
                                                            </div>
                                                            <div class="d-inline-block"  v-else>
                                                                <span class="d-block font-14 font-weight-medium">{{
                                                                        data.trackDescription }} </span>
                                                            </div>
                                                            <a class="float-right ml-3 mr-3" title=""
                                                                data-toggle-tooltip="tooltip"
                                                                data-original-title="Click here to view track Location(s)">
                                                                <em class="pl-2 pt-1  text-secondary"
                                                                    :class="CSStrackBy(data.trackAgent)"
                                                                    style="font-size:22px;cursor:pointer;"></em>
                                                            </a>

                                                        </td>

                                                        <td style="max-width:140px" class="newtimetrack">
                                                            <div class="hs_tag tagsModals" v-if="tagShow === data.trackId"
                                                                ref="tagsModals">

                                                                <multiselect ref="tagsMultiselect" :close-on-select="false"
                                                                    :placeholder="$t('selectTags')" :multiple="true"
                                                                    class="form-group tt_ddr_1 col border-0 tt_bod-right"
                                                                    v-model="timerTags" :options="store.getTags"
                                                                    label="tagName" track-by="tagId" :show-labels="false"
                                                                    id="exampleFormControlSelect1">
                                                                </multiselect>
                                                                <div data-v-0f960004="" class="ml-2 squarebtn"><a
                                                                        data-v-0f960004=""
                                                                        class="round-icon-small btn-info border-radius-0"
                                                                        @click="saveTags(data.trackId)"><em
                                                                            data-v-0f960004="" alt=""
                                                                            class="fa fa-save text-white pt-7 font-18"></em></a>
                                                                </div>

                                                            </div>
                                                
                                                            <div class="dottedbod_rt w-100 row">
                                                                <div class="float-left tagspanel" v-if="data.trackType!='Break'">
                                                                    <em @click="data.trackType=='Break' ? '': tagSelectBoxOpen(data.trackId)" 
                                                                        :class="{ 'tt_lisitng_icons ico_listtags float-left': (data.tag_name != null), 'tt_lisitng_icons ico_listtags-o float-left': (data.tag_name == null) }" :style="data.trackType=='Break'? '': 'cursor:pointer'"></em>

                                                                    <template
                                                                        v-for="(tags, tagIndex) of arrayConvert(data.tag_name)">
                                                                        <span v-if="tagIndex < 2">
                                                                            {{ tags }}
                                                                        </span>
                                                                        <a class="linkcss" v-if="tagIndex == 2"
                                                                            @click="pushTagsData(data.trackId + 'abc', arrayConvert(data.tag_name))">{{
                                                                                arrayConvert(data.tag_name).length - 2 }}
                                                                            {{ $t('More') }}
                                                                        </a>
                                                                    </template>
                                                                    <div v-if="showTagsToltip == data.trackId + 'abc'" class="viewmoretagsbase" >
                                                                        <div class="popover-arrowss"></div>                                                                        
                                                                        <div class="viewmoretags">
                                                                        <template
                                                                            v-for="(tags) of showMoreTagData[data.trackId + 'abc']">
                                                                            <span id="bottom" :title="tags">{{ tags }}
                                                                            </span>
                                                                        </template>
                                                                    </div>
                                                                </div>
                                                                </div>


                                                                <div class="float-left">
                                                                    <em :class="{ 'tt_lisitng_icons ico_listdollar float-left': (data.billableMark == '$'), 'tt_lisitng_icons ico_listdollar-o float-left': (data.billableMark != '$') }"
                                                                        @click="updateIsbillable(data.trackId, data.billableMark != '$' ? 'Billable' : '')" style="font-size:22px;cursor:pointer;"></em>
                                                                </div>
                                                                <div class="float-left" v-if="store.getTimer.enableBreak == 'yes'">

                                                                    <em :class="{ 'tt_lisitng_icons ico_listcup1 float-left': (data.trackType == 'Break'), 'tt_lisitng_icons ico_listcup2 float-left': (data.trackType != 'Break') }"></em>
                                                                </div>
                                                            </div>
                                                        </td>

                                                        <td style="max-width:60px">
                                                            <span class="font-14 font-weight-medium d-flex pt-3">
                                                                <em class="tt_lisitng_icons ico_listclock float-left mr-2"></em>
                                                                {{ data.startTime }} - {{ data.stopTime }}
                                                            </span>
                                                        </td>
                                                        <td class="timetracked" style="max-width:60px"><span
                                                                :class="{ 'green mt-1': (data.pausedHours > 0) }">{{
                                                                    data.totalHours }}</span></td>
                                                                    <!-- <td v-if="store.getTimer.enableBreak != 'yes'">{{ convertToTimeFormatHHMMSS(data.pausedHours) }}</td> -->
                                                        <td style="max-width:60px">
                                                            <!-- {{ data }} -->
                                                            <div v-if='(data.projectStatus != "INACT" || data.projectStatus != "DEL") && data.trackType == "TimeTrack" || (data.trackType == "Break" && data.takenBreak>0)'>
                                                                <div v-if='data.statusCode != "TRACK_ENTRY_APPROVED"'>
                                                                    <div class="float-left pt-3"
                                                                        @click="startTimer(data)"
                                                                        v-if='!((data.statusCode == "TRACK_ENTRY_ADD_MANUAL_PENDING" && item.approvalTypeForTime == "Timesheet"))'>
                                                                        <em class="tt_lisitng_icons ico_listplaytime float-left" style="font-size: 22px;cursor:pointer;"
                                                                            :trackId=data.trackId
                                                                            :trackDesc=data.trackDescription
                                                                            :trackBillable=data.isBillable
                                                                            :projectId="data.projectId"
                                                                            :taskId=data.taskId></em>
                                                                    </div>
                                                                    <div v-else>

                                                                        <a title="ContinueEntry"
                                                                            class="continuetimer"><em
                                                                                class="fa fa-clock-o text-light action-icon"
                                                                                style="color: #e0e8f0 !important; font-size:22px;cursor:pointer;"></em></a>
                                                                    </div>
                                                                </div>
                                                                <div v-else>

                                                                    <a title="ContinueEntry"
                                                                        class="continuetimer"><em
                                                                            class="fa fa-clock-o text-dark action-icon" style="font-size:22px;cursor:pointer;"></em></a>
                                                                </div>
                                                            </div>
                                                        </td>
                                                        
                                                        <td class="text-center mobile-action">
                                                            <span class="actions mobileaction">
                                                                <em aria-hidden="true"></em>

                                                                <em class="fa fa-ellipsis-h action_icon" aria-hidden="true"
                                                                    @click="ToggleActionButton(data.trackId)"></em>
                                                                <span class="action-list-box" style="overflow: visible;"
                                                                    v-bind:style="GetIndex === data.trackId ? 'width: 393px; display: block; opacity: 1;' : 'display: none; opacity: 0;'">

                                                                    <span class="list-actions p-0" id="action-list">
                                                                        <a class=" actions-onclick" data-toggle="modal"  @click="duplicate(data,i)"
                                                                            href="javascript:;" v-if="data.trackType!='Break'">
                                                                            <!-- <em class="fa fa-copy mr-1 text-primary"></em> -->
                                                                               <span class="text">{{$t('Duplicate')}} </span>
                                                                        </a>
                                                                        <a class=" actions-onclick" data-toggle="modal" 
                                                                            @click="UpdateOrDeleteEntries(data.trackId)"  href="javascript:;">
                                                                            <!-- <em class="fa fa-trash mr-1 text-primary"></em> -->
                                                                            <span class="text"> {{$t('Delete')}}</span>
                                                                        </a>
                                                                        <!-- <em class="fa fa-times close close-action" aria-hidden="true" @click="ToggleActionButton(GetIndex)"></em> -->
                                                                    </span> </span>
                                                            </span>
                                                        </td>

                                                    </tr>

                                                    <TimeTrackChildTable v-if="isExpendCollapsed(data.trackId) && store.$state.expendListRow===true"
                                                        :key="data.trackId + 'check'"
                                                        :list="listData[data.trackId + 'abc']">
                                                    </TimeTrackChildTable>

                                                </template>

                                                <!-- <tr v-for="(data,i) in item.entryList" :key="i"><td>{{ data }}</td></tr> -->
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="w-100 float-right flex-row-reverse mb-2 totalactivity_rt">
                <div class="float-right tagspanel">
                    
                    <span>{{$t('PaidTime')}}: <strong>{{ convertToTimeFormatHHMMSS(item.paidTime) }}</strong></span>                  
                    <span>{{$t('UnpaidTime')}}: <strong>{{ convertToTimeFormatHHMMSS(item.unPaidTime) }}</strong></span>
                    <span class="px-0">|</span>
                    <span>{{$t('TotalTime') }}: <strong>{{ convertToTimeFormatHHMMSS(item.totalTime) }}</strong></span>
                    <span v-if="store.getTimer.enableBreak!='yes'"> Paused Hours: {{ convertToTimeFormatHHMMSS(item.pausedHours) }}</span>

                </div>

            </div>


        </div>
        <loader :isVisible="getLoadingStatus"></loader>
        <DuplicateEntry v-bind:manualData="duplicateEntry" v-if="duplicateShow" v-on:close="duplicateShow = false"/>
    </section>
</template>
<style scoped> .tooltip-text {
     visibility: hidden;
     position: absolute;
     z-index: 1;
     width: 100px;
     color: white;
     font-size: 12px;
     background-color: #192733;
     border-radius: 10px;
     padding: 10px 15px 10px 15px;
 }

 .hover-text:hover .tooltip-text {
     visibility: visible;
 }

 .hover-text {
     position: relative;
     display: inline-block;
     margin: 40px;
     font-family: Arial;
     text-align: center;
 }
</style>
<script>
import { defineComponent, onMounted, reactive, ref } from '@vue/composition-api'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import { storeToRefs } from 'pinia';
import moment from "moment";
import TimeTrackChildTable from './TimeTrackChildTable.vue';
import { getPreviousDay } from "@/assets/common/vue-common.js"
import DataService from '../../services/DataService.js'
import DuplicateEntry from './modals/DuplicateEntry.vue';
import i18n from '../../localization/index'
export default defineComponent({
    components: { TimeTrackChildTable, DuplicateEntry },


    setup() {
        const store = userTimerInfo();
        const { getTimerListing, getUserInformation, getTimer, getLoadingStatus, getTimerListingFromTrackId ,timerInfo,getTimerRunResponse} = storeToRefs(store)
        onMounted(() => {
            const url = `dateType=${store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
            store.getTimerListing1(url)

        })

        const convertToTimeFormatHHMMSS = (seconds) => {
            if (seconds === 0 || seconds < 0) {
                return "0";
            } else {
                const hours = Math.floor(seconds / 3600);
                const minutes = Math.floor((seconds % 3600) / 60);
                const remainingSeconds = seconds % 60;

                return `${hours}:${minutes}:${remainingSeconds}`;
            }
        };
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: 0,
            format: 0,
            date: null,
            trackId: 0,
            startTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            stopTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            companyId: null,
            currentDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: getUserInformation.value.id,
            createdBy: getUserInformation.value.id,
            linkedTrackId: null,
            totalHours: 0,
            timeformat: 12,
            TagIds: null,
            isForcedUpdate: null,
            type: "TimeTrack",
            BreakDate: null,
            BreakName: null,
            BreakId: 0,
            BreakStartTime: 0,
            Shift_ID: 0,
            SelectedDate: "",
            Break_Shift: "",
            BreakStopTime:moment().format("YYYY-MM-DD HH:mm:ss"),
            showMoreTagData: []
        })
        async function startTimer(obj) {
            var vm=this;
            Object.assign(trackerData, obj)
            trackerData.CreatedBy = getUserInformation.value.id
            trackerData.createdBy = getUserInformation.value.id
            store.$state.loading = true
            trackerData.trackId = 0
            trackerData.totalHours = 0
            trackerData.type = trackerData.trackType
            if(trackerData.trackType=='Break'){
                if(timerInfo.value.trackType=='Break'){
                    this.ShowAlert(this.$t('BreakStartedAlready'), "warning", true, this.$t('Alert'));
                }else{
                trackerData.BreakStartTime= moment().format("YYYY-MM-DD HH:mm:ss")
                trackerData.BreakStopTime= moment().format("YYYY-MM-DD HH:mm:ss")
                trackerData.BreakId = obj.breakId
                trackerData.BreakName = obj.breakName
                trackerData.BreakDate=moment().format("YYYY-MM-DD HH:mm:ss")
                this.confirmR(this.$t("ConfirmBreakContinue"), true, (this.$t("Send") + "  " + this.$t("Records")),async function () {
                await store.submitProjectInfo(vm.trackerData)
                if (timerInfo.value.statusCode == "ACT" && getTimerRunResponse.value.status != 1) {
                    timerInfo.value.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
                        timerInfo.value.totalHours = store.$state.timerCountInSec
                        timerInfo.value.timerStatus = 'pause'
                        timerInfo.value.createdBy = trackerData.CreatedBy
                        timerInfo.value.type = timerInfo.value.trackType
                        timerInfo.value.currentDate= moment().format("YYYY-MM-DD HH:mm:ss")
                        await store.submitProjectInfo(timerInfo.value)
                       store.stopTimer()
                    }
                    vm.$emit('timerClose')
                    if (getTimer.value) {
                setTimeout(() => {
                    vm.$emit('timerOpen')
                }, 1);
            }
                })
            }

            }else{
            trackerData.startTime = moment().format("YYYY-MM-DD HH:mm:ss")
            trackerData.stopTime = moment().format("YYYY-MM-DD HH:mm:ss")
           if(timerInfo.value.trackType=='TimeTrack'){
            this.ShowAlert(this.$t('TimerStartedAlready'), "success", true, this.$t('Alert'));
                }else{
            this.confirmR(this.$t("ConfirmTimerContinue"), true, (this.$t("Send") + "  " + this.$t("Records")),async function () {
                if (timerInfo.value.statusCode) {
                    timerInfo.value.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
                        timerInfo.value.totalHours = store.$state.timerCountInSec
                        timerInfo.value.timerStatus = 'stop'
                        timerInfo.value.createdBy = trackerData.CreatedBy
                        timerInfo.value.type = timerInfo.value.trackType
                        timerInfo.value.BreakStartTime= moment().format("YYYY-MM-DD HH:mm:ss")
                        timerInfo.value.BreakStopTime= moment().format("YYYY-MM-DD HH:mm:ss")
                        timerInfo.value.currentDate= moment().format("YYYY-MM-DD HH:mm:ss")
                        await store.submitProjectInfo(timerInfo.value)
                        store.breakStopTimer()
                    }
             store.submitProjectInfo(vm.trackerData)
            vm.$emit('timerClose')
            if (getTimer.value) {
                setTimeout(() => {
                    vm.$emit('timerOpen')
                }, 1);
            }
        })
    }
    }
            store.$state.loading = false

        }
        //Change date type and get data 
        function getListByDateType(type) {
            this.store.$state.dateFilterType = type
            const url = `dateType=${type}&search=&toDate=&fromDate=&newTrackId=0`
            store.getTimerListing1(url)
        }
        //Get data from trackId and append in another table

        return {
            getTimerListing,
            convertToTimeFormatHHMMSS,
            store,
            trackerData,
            startTimer,
            getListByDateType,
            getLoadingStatus,
            getTimerListingFromTrackId,
            getPreviousDay,
            moment



        }
    },

    data() {
        return {
            collapsedSections: [false], // Each index represents a section, initialized as not collapsed
            selectedCheckboxes: [],
            expendCollapsedSections: [false],// Expend tr in table
            listData: [],
            arrayData: [],
            showAllTags: false,
            GetIndex: null,
            isActiveActionButton: false,
            tagShow: '',
            timerTags: [],
            showTagsToltip: '',
            duplicateShow:false,
            duplicateEntry:''
        };
    },

    methods: {
        async tracklocation(index, data) {
            this.expendCollapsedSections = [false]
            this.store.$state.expendListRow = true
            await this.store.GetTimerListingByTrackId(`?trackId=${data.trackId}&DateType=${this.store.$state.dateFilterType}`)
            this.arrayData[index] = this.getTimerListingFromTrackId ? this.getTimerListingFromTrackId[0].entryList : []
            //console.log("value",this.arrayData)
            this.listData = this.arrayData
            //console.log("value", this.expendCollapsedSections)
            this.expendToggleCollapse(data.trackId)
            
        },
        arrayConvert(event) {
            if (event) {
                let convert = event.split(',')
                return convert
            }
            else {
                return []
            }
        },
        ToggleActionButton(index) {
            this.isActiveActionButton = !this.isActiveActionButton;
            if (index == this.GetIndex) {
                this.GetIndex = ''; // Set the index if needed
            } else {
                this.GetIndex = index
            }
        },
        expendToggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.expendCollapsedSections, index, !this.expendCollapsedSections[index]);
        },
        isExpendCollapsed(index) {
            return this.expendCollapsedSections[index];
        },
        toggleCollapse(index) {
            // Toggle the collapse state of the section at the given index
            this.$set(this.collapsedSections, index, !this.collapsedSections[index]);
        },
        isCollapsed(index) {
            // Check if the section at the given index is collapsed
            return this.collapsedSections[index];
        },
        rowClass(rowNo) {
            let rowClass = rowNo % 2 === 0 ? "gray" : "";
            return rowClass;
        },
        tagSelectBoxOpen(id) {

            if (this.tagShow != id) {
                this.tagShow = id
            } else {
                this.tagShow = ''
            }
            console.log("tags",this.tagShow)
        },
        async saveTags(id) {
            let tagIds = ''
            this.timerTags.map((items, index) => {
                tagIds += index != 0 ? ',' + items.tagId : items.tagId
            })
            let data = `ids=${id}&ActionType=Tag&tagIds=${tagIds}`
            await this.store.UpdateOrDeleteEntries(data)
            if (this.store.tagsSaveForListingResponse) {
                const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                this.store.getTimerListing1(url)
                this.timerTags = []
                this.tagShow = ''
            }

        },
        pushTagsData(id, data) {
            this.showTagsToltip = id
            this.showMoreTagData = []
            let tagsArray = []
            tagsArray[id] = data.splice(2, data.length)
            this.showMoreTagData = tagsArray
            console.log("check", this.showMoreTagData)

        },
        entryStatusClass(entryType) {

            let entryStatusClass = "";
            switch (entryType) {
                case "TRACK_ENTRY_REJECTED":
                    entryStatusClass = "delpending";
                    manualEntry = "";
                    tip = "Rejected";
                    break;
                case "TRACK_ENTRY_UPDATE_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "UpdateRequestPending";
                    break;
                case "TRACK_ENTRY_DELETE_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "DeleteRequestPending";
                    break;
                case "TRACK_ENTRY_ADD_MANUAL_PENDING":
                    entryStatusClass = "high-profile-bar";
                    manualEntry = "";
                    tip = "AddManualRequestPending";
                    break;
                case "TRACK_ENTRY_APPROVED":
                    entryStatusClass = "lgtr-approved";
                    manualEntry = "";
                    tip = "Approved";
                    break;
                default:
                    entryStatusClass = "";
                    break;
            }
            return entryStatusClass
        },
        EntryTypeColor(track) {
            let trackBy = track.trackAgent
            let EntryTypeColor = "";
            switch (trackBy) {
                case "Web":
                    EntryTypeColor = track.pausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                    break;
                case "web":
                    EntryTypeColor = track.pausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                    break;
                case "Web_Manual":
                    EntryTypeColor = "gt-manual-bar";
                    break;
                case "timesheet":
                case "timesheet_mobile":
                    EntryTypeColor = "gt-timesheet-bar";
                    break;
                case "xamarin":
                case "linux":
                case "silentapp":
                    EntryTypeColor = track.PausedHours > 0 ? "gt-paused-bar" : "gt-automatic-bar";
                    break;
                case "mamarinmanual":
                    EntryTypeColor = "gt-manual-bar";
                    break;
                // Add other cases as needed

                default:
                    EntryTypeColor = "gt-automatic-bar";
                    break;
            }
            return EntryTypeColor;
        },
        CSStrackBy(data) {

            let trackBy = "";
            switch (data) {
                case "desktop":
                case "linux":
                    trackBy = "fa fa-desktop";
                    break;
                case "Web":
                    trackBy = "fa fa-globe";
                    break;
                case "web":
                    trackBy = "fa fa-globe";
                    break;
                case "Web_Manual":
                    trackBy = "fa fa-globe";
                    break;
                // Add other cases as needed

                default:
                    trackBy = "fa fa-mobile";
                    break;
            }
            return trackBy;
        },
