<template>
<div>
    <div class="my-popups">
        <div class="modal d-block ">
                    <div class="modal-dialog ui-draggable" style="display: inline-block;">
                        <div class="modal-content">
                            <div class="modal-header ui-draggable-handle">
                                <h5 id="ui-id-26" class="modal-title">{{ $t('AddTag') }}</h5><button type="button" title="" @click="$emit('close',false)" class="close" data-original-title="Close"><i class="fa fa-times" aria-hidden="true"></i></button>
                            </div>
                            <div id="backgroundPopup" class="modal-body" style="height: auto; min-height: 0px; max-height: none;">
                                <link href="/Content/css/CssBlack/colorpicker.css" rel="stylesheet" theme="" type="text/css">
                                <span class="user-guide">
                                    <a class="ancuserguide collapsed" data-toggle="collapse" href="#collapseReply" role="button" aria-expanded="false" aria-controls="collapseExample">User Guide</a>
                                    <div id="collapseReply" class="divancuserguide collapse">
                                        <div id="scrollbarreplyuserguide" class="custom-scrollbar-js mCustomScrollbar _mCS_6 mCS_no_scrollbar">
                                            <div id="mCSB_6" class="mCustomScrollBox mCS-dark-thick mCSB_vertical mCSB_inside" style="max-height: none;" tabindex="0">
                                                <div id="mCSB_6_container" class="mCSB_container mCS_y_hidden mCS_no_scrollbar_y" style="position:relative; top:0; left:0;" dir="ltr">
                                                    <div class="con">
                                                        <div class="DefaultUserGuide">
                                                            On this screen, you can add a new tag. Tags are meant to help in reaching the entities where tags are applied and are used to keep various kinds of entities in a single place for rapid access. To add a new tag, enter the details in the following fields. Here are the fields:<br><br>
                                                            1.<strong> Tag Name - </strong>Enter the tag name<br><br>
                                                            2.<strong> Status - </strong>Select Status of the tag<br><br>
                                                            3.<strong> Tag Description- </strong>Add a description for the Tag<br><br>
                                                            4.<strong> Save - </strong>Click to create the new tag<br><br>
                                                            5.<strong> Cancel - </strong>Click on ‘Cancel’ to cancel the tag.

                                                        </div>
                                                    </div>
                                                </div>
                                                <div id="mCSB_6_scrollbar_vertical" class="mCSB_scrollTools mCSB_6_scrollbar mCS-dark-thick mCSB_scrollTools_vertical" style="display: none;">
                                                    <div class="mCSB_draggerContainer">
                                                        <div id="mCSB_6_dragger_vertical" class="mCSB_dragger" style="position: absolute; min-height: 30px; height: 0px; top: 0px;">
                                                            <div class="mCSB_dragger_bar" style="line-height: 30px;"></div>
                                                        </div>
                                                        <div class="mCSB_draggerRail"></div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </span>
                                <!-- Show Any Msg Here -->
                                <!-- End -->

                                <section id="main-content" class="main-content">
                                    <section class="main-content">
                                        <div class="container-fluid p-0">
                                            <div class="p-0">
                                               
                                                <ValidationObserver  v-slot="{ handleSubmit }">
                                                    <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                                                        <div class="row">
                                                            <div class="col-lg-6">
                                                                <div class="form-group">
                                                                    <ValidationProvider :rules="'required'" v-slot="{ errors }" name="Tag name">
                                                                        <label class="" for="TagName">{{ $t('TagName') }}</label>:<span class="mandatory">*</span><span class="field-validation-valid invalid-feedback" data-valmsg-for="TagName" data-valmsg-replace="true"></span>
                                                                        <input type="text" v-model="tagsData.tagName" class="form-control" :class="errors[0]  ? 'is-invalid' : '' " maxlength="100">
                                                                        <span class="invalid-feedback">{{ errors[0] }}</span>
                                                                    </ValidationProvider>
                                                                </div>
                                                            </div>
                                                            <div class="col-lg-6">
                                                                <div class="form-group">
                                                                    <label class="" for="TagName">{{ $t('status_id') }}</label>:<span class="mandatory"></span><span class="field-validation-valid invalid-feedback" data-valmsg-for="TagName" data-valmsg-replace="true"></span>
                                                                    
                                                                    <multiselect  :placeholder="$t('SelectStatus')" class="form-group singleselectlook" v-model="tagsData.Status" :options="tagsOption" label="name" track-by="id" :show-labels="false" id="exampleFormControlSelect1">
                                                                    </multiselect>
                                                                    
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="row">
                                                            <div class="col-lg-12">
                                                                <div class="form-group">
                                                                    <label>{{ $t('TagDescription') }}:</label>
                                                                    
                                                                        <textarea name="tagDescription"  v-model="tagsData.tagDescription" class="form-control" :placeholder="$t('PleaseEnterDescription')" maxlength="2000"></textarea>
                                                                        
                                                                    
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div class="row flex-row-reverse">
                                                            
                                                                <div class="text-end text-right col-12">
                                                                    <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</button>
                                                                    <button @click="$emit('close',false)" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
