<style>
.singleselectlook .multiselect__select::before{z-index:99;}
.singleselectlook .multiselect__tags{padding: 8px 30px 0 8px !important;min-height: 36px !important;}
.singleselectlook .multiselect__input{margin-bottom: 0px;}
</style>

<script>
import { defineComponent,} from '@vue/composition-api'
import { ValidationProvider, extend, ValidationObserver} from 'vee-validate';
import Multiselect from 'vue-multiselect'
import { userTimerInfo} from "../../../stores/TimeTrack"
import Alert from '../../Common/Alert.vue';
extend('required', {
    validate(value) {
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };
        
    },
    message: 'The {_field_} must be required'
    
    ///computesRequired: true
});
export default defineComponent({
    name: "Tags",
    components: {
        Multiselect,
        ValidationProvider,
        ValidationObserver,
        Alert
    },
    data() {
        return {
            tagsOption:[{
                name:"Active",
                id:'1001'
            },
            {
                name:"Inactive",
                id:'1002'
            }

            ],
            tagsData:{
                tagName:'',
                Status:{
                name:"Active",
                id:'1001'
            },
                tagDescription:''
            }
        }     

    },
    created: function () {

    },
    methods: {
       async submit(){
            this.tagsData.Status=this.tagsData.Status? this.tagsData.Status.id : ''
            await this.store.tagsSave(this.tagsData)
            this.store.tagGetForTimer()
            if(this.store.getTagSaveRes== "added Successfully"){
             this.$emit('close',false)
            }
        }

    },
    setup() {
        const store = userTimerInfo();
        return {
            store,

        }
    },
    });
</script>