<template lang="en">
<div>
    
    <div v-if="automaticShowBreak">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3 p-0">
            <div class="heading-border">
                <h5 class="h5"><span id="SProjectDetailTitleSpan">{{$t('Break_MyDashboard_Module')}}</span>
                <a class="link float-right font-12 mt-1" @click="changeBreakModal"><em class="fa fa-plus mr-2"></em>Add Manual</a></h5>
            </div>
        </div>
       
        <!-- <button type="button" class="close" style="background: none;color: #fff !important;" @click="$emit('close',{breakTimer:false,modalClose:false})"><em aria-hidden="true" class="fa fa-times"></em></button> -->
        <div class="col-12 p-0 float-left" id="divModalTimetrack">
            <!-- <dynamic-form lang="en" :buttons="buttons" ref="form" :schema="FormSchema" v-on:OnSubmit="submit()">
                <template v-slot:tgslot-button="{ data }">

                    </template>
            </dynamic-form> -->
            <label>Select Break</label>
                <div class="w-100 d-flex dynamic-multiselect" id="divModalTimetrack">
                    <multiselect @input="singleBreak" :placeholder="$t('selectBreak')" :multiple="false" class="multiselect p-0 w-100" v-model="singleBreakSelect" :options="store.getBreakTimeList" label="name" track-by="value" :show-labels="false" id="exampleFormControlSelect1">
                        <template slot="option" slot-scope="props">
                                            <div class="option__desc"><span class="option__title">{{
                                                props.option.name }} ({{ props.option.breakDuration}})
                                                 <i :class="props.option.isPaid ? 'fa fa-dollar text-muted' : 'fa fa-dollar text-success'"></i></span> 
                                                  
                                              </div>
                                        </template>
                    </multiselect>
                    <span class="val_msg">{{ singleBreakError? $t('BreakRequired') : '' }}</span>
                    
                </div>
            <div class="start-break col-12 float-left p-0" v-on:click="submitForm">
                <a class="btn btn-primary text-white btn-lg starttimer w-100" id="btnBreakStart" title="" style="cursor: pointer" data-toggle-tooltip="tooltip" type="submit">
                    <i class="fa fa-coffee pr-2"></i><span>{{$t('STARTBREAK')}}</span>
                </a>

            </div>
        </div>
    </div>
    <div v-else>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left mb-3 p-0">
            <div class="heading-border">
                <h5 class="h5"><span id="SProjectDetailTitleSpan">{{$t('ManualBreak')}}</span></h5>
            </div>
        </div>
        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
            <form v-on:submit.prevent="handleSubmit(breakSubmit)" ref="form">
                <!-- <button type="button" class="close" style="background: none;color: #fff !important;" @click="$emit('close',{breakTimer:false,modalClose:false})"><em aria-hidden="true" class="fa fa-times"></em></button> -->
                <label>Select Break</label>
                <div class="w-100 d-flex dynamic-multiselect" id="divModalTimetrack">
                   
                    <multiselect @input="dateSelected" :placeholder="$t('selectBreak')" :multiple="true" class="multiselect p-0 w-100" v-model="selectBreak" :options="store.getBreakTimeList" label="name" track-by="value" :show-labels="false" id="exampleFormControlSelect1">
                        <template slot="option" slot-scope="props">
                                            <div class="option__desc"><span class="option__title">{{
                                                props.option.name }} ({{ props.option.breakDuration}}) 
                                                <i :class="props.option.isPaid ? 'fa fa-dollar text-muted' : 'fa fa-dollar text-success'"></i>
                                            </span> 
                                                  
                                              </div>
                                        </template>
                    </multiselect>
                    <span class="val_msg">{{ breakError? $t('BreakRequired') : '' }}</span>
                    
                </div>


                <div class="col-12 p-0 float-left">
                    <div class="form-group"><label>{{$t('SelectDate')}}:</label> 
                        <div class="input-group mb-3">
                            <input type="date" v-model="breakData.BreakDate" class="form-control hasdatetimepicker vue" id="datepicker"> 
                    <div class="input-group-append">
                        <span :title="$t('SelectDate')" class="input-group-text datepickerbutton">
                            <em class="fa fa-calendar font-16"></em></span>
                        </div>
                    </div>
                </div>
            </div>




                <!-- <div class="col-12 p-0" id="divModalTimetrack">

                    <ValidationProvider :rules="'required'" v-slot="{ errors }">
                        <input type="date" v-model="breakData.BreakDate" class="border-0 font-14" />
                        <span class="val_msg">{{ errors[0] ? $t('ExpDateRequired') : '' }}</span>
                    </ValidationProvider>

                </div> -->



                <div class="row flex-row-reverse">

                    <div class="text-end text-right col-12">
                        <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</button>
                        <button @click="$emit('close')" type="button" class="btn btn-danger" style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</button>
                    </div>

                </div>
            </form>
        </ValidationObserver>
    </div>

</div>
</template>
<script>
import {
    defineComponent,
    onMounted,
    reactive,
    ref,
    onServerPrefetch
} from '@vue/composition-api'
import Multiselect from 'vue-multiselect'
import {
    userTimerInfo
} from "../../../stores/TimeTrack";
import {
    storeToRefs
} from "pinia";
import moment from "moment"
import {
    timeToSeconds,
    getSecondsFromDate
} from "@/assets/common/vue-common.js"
import {
    ValidationProvider,
    extend,
    ValidationObserver
} from 'vee-validate';
extend('required', {
    validate(value) {
        console.log("check",value)
        return {
            required: true,
            valid: ['', null, value.length>0].indexOf(value) === -1
        };
    },
    message: '{_field_} is required',
    computesRequired: true
});
export default defineComponent({
    name: "Break Timer",
    components: {
        Multiselect,
        ValidationProvider,
        ValidationObserver,
    },
    props: {
        userId: {
            type: Number,
            default: "my title",
        },
        timerModule: {
            type: Boolean
        },
        trackDataPouse: {
            type: Object
        },
        automaticBreak:{
            type:Boolean
        }
    },
    data() {
        return {
           
            singleBreakError:false,
            buttons: false,
            //error: '',
            breakData: {
                BreakDate: moment().format("YYYY-MM-DD"),
                BreakId: [],
                trackAgent:"Web_Manual"
            },
            breakError:false,
            dateError:false,
            selectBreak:[],
            singleBreakSelect:'',
            automaticShowBreak: this.automaticBreak,
            trackDataProps:this.trackDataPouse
            // trackDataProps:this.trackDataPouse

        }
    },
    setup(props, context) {
        console.log("check",context.attrs)
        //if true open components from header timer else open from add timetrack module
        const breakModule = context.attrs.timerModule
       // const trackDataProps = reactive(context.attrs.trackDataPouse)
        const store = userTimerInfo();
        const formResponse = ref()
        const duration = ref()
        const trackerData = reactive({
            projectId: null,
            trackDescription: "",
            taskId: null,
            isBillable: false,
            format: false,
            date: null,
            trackId: 0,
            startTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            stopTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            companyId: null,
            currentDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            trackAgent: "web",
            timerStatus: "start",
            CreatedBy: context.attrs.userId,
            linkedTrackId: null,
            TotalHours: 0,
            timeformat: "12",
            TagIds: null,
            isForcedUpdate: null,
            type: "Break",
            BreakDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            BreakName: null,
            BreakId: 0,
            BreakStartTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            BreakStopTime: moment().format("YYYY-MM-DD HH:mm:ss"),
            Shift_ID: 0,
            SelectedDate: moment().format("YYYY-MM-DD HH:mm:ss"),
            Break_Shift: ""
        })

        const {
            getBreakTimeList,
            getTimerRunResponse
        } = storeToRefs(store)
        const breakTimer = reactive({
            breakTime: ''
        })
        const state = ref({
            submitted: false,
            error: ''
        })
        onMounted(() => {

        })

        return {
            breakTimer,
            state,
            getBreakTimeList,
            trackerData,
            getTimerRunResponse,
            formResponse,
            store,
            duration,
            breakModule,
            //trackDataProps

        }
    },

    created: function () {
console.log("trackDataPouse",this.trackDataPouse)
    },
    methods: {
        changeBreakModal(){
            this.automaticShowBreak = this.automaticShowBreak =! this.automaticShowBreak
        },
        submitForm:async function () {
           this.singleBreak();
                if (this.singleBreakSelect) {
                    console.log("checkkkk",this.singleBreakSelect)
                    // const time = this.store.getBreakTimeList.find((data) => data.name == this.FormSchema[0].Data[0].value.label)
                    this.trackerData.BreakId = this.singleBreakSelect.value
                    this.trackerData.BreakName = this.singleBreakSelect.name
                    this.trackerData.Shift_ID = this.store.timerInfo.shift_ID
                    this.trackerData.trackDescription = this.singleBreakSelect.name
                    this.trackerData.isBillable = this.singleBreakSelect.isPaid
                    await this.store.submitProjectInfo(this.trackerData);
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)
                    if (this.store.timerInfo.statusCode == "ACT" && this.store.getTimerRunResponse?.status != 1) {
                        this.trackDataProps.stopTime = moment().format('YYYY-MM-DD HH:mm:ss')
                        this.trackDataProps.totalHours = this.store.$state.timerCountInSec
                        this.trackDataProps.timerStatus = 'pause'
                        this.trackDataProps.trackId = this.store.timerInfo.trackId
                        this.trackDataProps.createdBy = this.trackerData.CreatedBy
                        this.trackDataProps.projectId = typeof this.trackDataProps.projectId == "object" && this.trackDataProps.projectId ? this.trackDataProps.projectId.projectId : this.trackDataProps.projectId
                        this.trackDataProps.taskId = typeof this.trackDataProps.taskId == "object" && this.trackDataProps.taskId ? this.trackDataProps.taskId.value : this.trackDataProps.taskId
                        this.trackDataProps.trackDescription = this.store.timerInfo.trackDescription
                        await this.store.submitProjectInfo(this.trackDataProps)
                        this.store.stopTimer()
                    }
                    await this.store.getTimerInfo();
                    if (this.store.timerInfo.trackType == 'Break') {
                        
                        let seconds = getSecondsFromDate({
                            startDate: this.store.getTimer?.dateStart,
                            endDate: moment()
                        }, 'seconds')
                        this.store.$state.breakTimerCountInSec = timeToSeconds(this.store.timerInfo.remainingBreakDuration) - seconds
                        this.store.breakStartTimer()
                        console.log("checkkkkkkkkkkkk",this.store.$state.breakTimerCountInSec)

                        //breakTimer for timer and breack show in header
                        this.$emit("close", {
                            "breakTimer": true,
                            "modalClose": false
                        })
                    } else {
                       // this.error = this.store.getTimerRunResponse?.userMsg;
                    }

                }
          
        },
        async breakSubmit() {
            if(this.selectBreak.length==0){
            this.breakError =true
        }else{
            this.breakError =false
        }
        if(!this.breakError){

            this.breakData.BreakId = []
            this.selectBreak.map((item) => {
                this.breakData.BreakId.push(item.value)
            })

            await this.store.addManualBreak(this.breakData);
            if(this.store.getAddManualBreakResponse.status==0){
                this.$emit("close", {
                            "breakTimer": true,
                            "modalClose": false
                        })
                    const url = `dateType=${this.store.$state.dateFilterType}&search=&toDate=&fromDate=&newTrackId=0`
                    this.store.getTimerListing1(url)
                this.breakModalPopup = false
                this.selectBreak =[]
                this.breakData.BreakDate = moment().format("YYYY-MM-DD")
            }
        }
            
        
        },
        dateSelected(){
            if(this.breakData.BreakDate){
                this.dateError = false
            }else{
                this.dateError = true
            }
        },
        breakSelect(){
            if(this.selectBreak.length>0){
                this.breakError = false
            }else{
                this.breakError = true
            }
        },
        singleBreak(){
            
            if(this.singleBreakSelect){
                this.singleBreakError = false
            }else{
                this.singleBreakError = true
            }
            
        }

    }

})
</script>
